.modal.stream-history-modal .modal-dialog {
  width: 1000px;
  max-width: 1000px;
  height: 485px;
}

@media screen and (max-width: 1000px) {
  .modal.stream-history-modal .modal-dialog {
    width: calc( 100vw - 50px );
    margin: 10px auto;
  }
  .modal.stream-history-modal .modal-dialog .modal-content {
    min-width: 300px;
  }
}
@media screen and (max-width: 768px) {
  .modal.stream-history-modal .stream-header {
    display: none;
  }
  .modal.stream-history-modal {
    display: block;
  }
  .modal.stream-history-modal .stream-row {
    position: relative;
    height: auto;
    padding: 10px;
  }
  .modal.stream-history-modal .stream-row div {
    display: block;
    border: none;
    line-height: 2.2rem;
    width: 100%;
    padding-left: 75px;
  }
  .modal.stream-history-modal .stream-row div:before {
    content: attr(data-title) ": ";
    position: absolute;
    font-weight: bold;
    left: 5px;
  }
}
