.message-upload-modal {
  position: relative;
  padding: 15px;
  color: black; }
  .message-upload-modal h2 {
    margin-top: 0;
    margin-bottom: 15px; }
  .message-upload-modal .status {
    display: block;
    text-align: center;
    height: 0;
    width: 100%;
    padding: 0 10px; }
  .message-upload-modal .status.showProgress {
    height: 10px;
  }
  .message-upload-modal .status .progress-bar {
    height: 100%;
    padding: 0;
    background-color: #00AA00; }
  .message-upload-modal .row label {
    display: inline-block;
  }
  .message-upload-modal .row label.labelLeft {
    color: #6a6a6a;
    font-size: 1.4rem;
    width: 65px; }
  .message-upload-modal .row div.filename {
    display: inline-block;
    vertical-align: middle;
    margin-top: -2px;
    font-weight: 300;
    line-height: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 0 5px;
    color: #4a4a4a;
    width: 175px;
    height: 30px;
    background: #fafafa;
    border: 1px solid #dfdfdf;
    border-radius: 2px; }
  .message-upload-modal .row input.file-input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1; }
  .message-upload-modal .row label.file-selection {
    cursor: pointer;
  }
  .message-upload-modal .row span.file-button {
    display: inline-block;
    margin-left: 10px;
    width: 90px;
    height: 30px;
    line-height: 30px;
    background: #9e9e9e;
    border-radius: 2px;
    color: #ffffff;
    text-align: center;
    font-size: 1.4rem; }
  .message-upload-modal .row span.file-button:hover {
    background: #7e7e7e; }
  .message-upload-modal .row input.title-input {
    font-size: 1.4rem;
    padding: 3px 5px;
    color: #4a4a4a;
    width: 278px; }
  .message-upload-modal .close-button {
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer; }
    .message-upload-modal .close-button i {
      font-size: 2rem;
      pointer-events: none; }
  .message-upload-modal .row.errorMessage {
    color: #d9534f;
    font-size: 13px;
    margin: -13px 0 0 79px;
  }
  .message-upload-modal .row .filename.error {
    border: 1px #d9534f solid;
  }
.message-upload-window .modal-footer .btn.submit {
  width: 130px;
}


