.intercom-launcher {
  backface-visibility: hidden;
  background: #1f8ceb;
  border-radius: 50%;
  bottom: 10px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.06), 0 2px 32px rgba(0, 0, 0, 0.16);
  cursor: pointer;
  font-family: Helvetica, Arial, sans-serif;
  height: 0;
  opacity: 0;
  outline: 0;
  position: fixed;
  right: 10px;
  transform: scale(0.5);
  transform-origin: center;
  transition: opacity 0.25s ease, transform 0.25s ease;
  width: 0;
  z-index: 2147483647;
}
.logout-container .intercom-launcher {
  background: #00C276;
}
.intercom-launcher:focus {
  outline: 0;
}
.intercom-launcher.intercom-booted {
  height: 35px;
  opacity: 1;
  transform: scale(1);
  width: 35px;
}
.intercom-launcher * {
  cursor: pointer;
}

.intercom-unread-count {
  backface-visibility: hidden;
  background: #fc576b;
  border-radius: 10px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  color: #fff;
  font-size: 12px;
  height: 20px;
  line-height: 20px;
  min-width: 20px;
  opacity: 0;
  padding: 0 4px;
  position: absolute;
  right: -2px;
  text-align: center;
  top: -2px;
  transform: scale(0.5);
  transform-origin: center;
  transition: opacity 0.15s ease, transform 0.15s ease;
}
.intercom-unread-count.active {
  opacity: 1;
  transform: scale(1);
}

.intercom-icon-open,
.intercom-icon-close {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 50%;
  border-radius: 50%;
  bottom: 0;
  position: absolute;
  top: 0;
  transition: transform 0.16s linear, opacity 0.08s linear;
  width: 100%;
}

.intercom-icon-open {
  background-image: url("data:image/png;base64,");
  opacity: 1;
  transform: rotate(0deg) scale(1);
}
.intercom-open .intercom-icon-open {
  opacity: 0;
  transform: rotate(30deg) scale(0);
}

.intercom-icon-close {
  background-image: url("data:image/png;base64,");
  opacity: 0;
  transform: rotate(-30deg);
  background-size: 35%;
}
.intercom-open .intercom-icon-close {
  opacity: 1;
  transform: rotate(0deg);
}
.intercom-launcher.mobile-media-toolbar-open {
  display: none;
}

@media (max-width:767px) {
  .intercom-launcher {
    bottom: 110px;
  }
}
@media (min-width:768px) {
  .intercom-launcher {
    bottom: 60px;
  }
}
.intercom-launcher.mobile {
  height: 45px;
  width: 45px;
  bottom: 10px;
}
