.modal-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: rgba(0, 0, 0, 0.1);
}

.modal-dialog {
  padding: 20px;
  top: 80px;
  max-width: 430px;
}

@media screen and (max-width: 767px) {
  .modal-dialog {
    padding: 0;
  }
}
@media screen and (max-width: 620px) {
  .modal .modal-dialog {
    max-width: calc(100vw - 20px);
    top: 0;
    margin: 10px auto;
  }
  .modal .modal-body.mobile {
    max-height: 80vh;
  }
}
@media screen and (max-height: 420px) and (orientation: landscape) {
  .modal .modal-dialog {
    max-width: calc(100vw - 20px);
    top: 0;
    margin: 10px auto;
  }
  .modal .modal-body.mobile {
    max-height: 80vh;
  }
}

.modal h2 {
  font-size: 2rem;
  font-family: "Lato", sans-serif;
  font-weight: 600;
  line-height: 2.6rem;
  letter-spacing: 0;
  margin: 0;
  padding: 3px 0 5px 0;
  color: #262626;
}
.modal .header-close-button {
  color: #000;
  position: absolute;
  right: 15px;
  top: 16px;
  cursor: pointer;
}
.modal .header-close-button i {
  font-size: 2rem;
  pointer-events: none;
}

.modal .modal-body {
  padding: 20px;
}

.modal .modal-body h4 {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  font-weight: 600;
  color: #262626;
  line-height: 2rem;
  margin: 10px 0 0 0;
}

.modal .modal-body p {
  margin: 5px 0;
  color: #606060;
  font-size: 1.6rem;
}
.modal .modal-body .row {
  margin: 0 0 10px 0;
  display: block;
}
.modal .modal-body .row.field-section {
  margin: 10px 0 0 0;
}
.modal .modal-body .alert {
  margin-bottom: 0;
}
.modal .modal-body .alert p {
  font-size: 1.4rem;
  margin: 10px 0;
}
.modal .modal-body .alert input[type = 'checkbox'] {
  margin-right: 3px;
}
.modal .continue-query {
  color: #000;
  font-weight: bold;
  font-size: 1.6rem;
}

.modal .modal-footer button {
  width: 80px;
  height: 34px;
  border-radius: 4px;
}
.modal .modal-footer button.okay {
  background: #0E83C9;
}
.modal .modal-footer button.disabled {
  background: #B3B3B3;
  border: 1px solid #dbdbdb;
}
.modal .modal-footer button.cancel {
  background: transparent;
  border: 1px solid #dbdbdb;
}
.modal .modal-footer .btn-delete {
  float: left;
  cursor: pointer;
  color: #cc0000;
  line-height: 32px;
  vertical-align: middle;
}


/* ---------------- */

.modal-content.single {
  width: 40%;
  min-width: 380px;
  max-width: 550px;
  height: 345px;
}

@media screen and (max-width: 992px) {
  .modal-content.single {
    height: 372px;
  }
}

.modal-content.multi {
  width: 40%;
  min-width: 380px;
  max-width: 550px;
  height: 420px;
}

.modal-content.stream {
  height: 450px;
  width: 80%;
}

.modal-content .close-modal {
  background-color: #5bc0de;
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  outline: 0;
  width: 35px;
  height: 30px;
  color: white;
  opacity: 0.2;
  border-radius: 2px;
  border-top-left-radius: 0;
  border-bottom-right-radius: 0;
  -moz-transition: opacity .5s;
  -o-transition: opacity .5s;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=20);
}

.modal-content .close-modal:hover {
  opacity: 1;
}

.modal-content .thank-you-text {
  color: #666;
  padding: 20px 5px;
  margin-top: 25px;
}

.modal-content .thank-you-text h1 {
  color: #428bca;
  font-size: 2em;
  margin: 0 0 10px;
}

.modal-content .thank-you-text h4 {
  color: #428bca;
}

.modal-content .thank-you-text .strong-words {
  color: #428bca;
  font-weight: bold;
}

.modal-content .thank-you-text .sub-text {
  margin-bottom: 30px;
  color: #5cb85c;
  font-weight: bold;
}

.modal-content .show {
  display: block;
}

.modal-content .stream-container {
  padding-top: 10px;
  overflow-y: auto;
  max-height: 410px;
}

.modal-content .stream-container h4 {
  font-weight: 300;
  margin-bottom: 30px;
}

.modal-content .stream-container h4.no-stream-history {
  margin: 0;
  text-align: center;
  line-height: 435px;
}

.modal-content .stream-header {
  padding-bottom: 10px;
  font-weight: bold;
  border-bottom: 2px solid #ddd;
  font-size: 15px;
}

.modal-content .stream-row-container {
  overflow: scroll;
  height: 361px;
  color: #999;
  font-size: 14px;
}

.modal-content .stream-row {
  height: 70px;
  border-top: 1px solid #ddd;
  line-height: 70px;
}

.modal-content .stream-row:first-child {
  border-top: none;
}

.modal-content .stream-time {
  width: 16%;
  padding-right: 2%;
}

.modal-content .stream-song {
  width: 20%;
  padding-right: 2%;
}

.modal-content .stream-artist {
  width: 16%;
  padding-right: 2%;
}

.modal-content .stream-station {
  width: 15%;
  padding-right: 2%;
}

.modal-content .stream-type {
  width: 10%;
  padding-right: 2%;
}

.modal-content .stream-device {
  width: 15%;
}

.modal-content ul {
  list-style: none;
  padding: 0;
}

.modal-content ul li div {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}

.modal-open {
  overflow: auto;
}

div[confirmation-modal] h1 {
  margin-top: 10px;
  font-size: 2.5rem;
  text-align: left;
  padding-bottom: 10px;
}

.stream-history-modal .modal-content {
  min-width: 380px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: white;
  border-radius: 3px;
  -webkit-box-shadow: 0px 3px 11px -1px rgba(194,194,194,0.65);
  -moz-box-shadow: 0px 3px 11px -1px rgba(194,194,194,0.65);
  box-shadow: 0px 3px 11px -1px rgba(194,194,194,0.65);
  z-index: 555;
}

.modal .modal-body.player-takeover-notification p {
  margin: 5px 0 0 0;
  font-size: 1.4rem;
}

.modal .modal-body.station-info.mobile .station-info-content {
  overflow: auto;
  max-height: calc( 80vh - 70px );
}
.modal .modal-body.station-info.mobile .audio-player-container {
  text-align: center;
}
.modal .modal-body.station-info.mobile i.play-button {
  font-size: 4.5rem;
}
