.locations-container .table-container table.table-hover {
  margin-bottom: 0;
  table-layout: fixed;
}
.locations-container .table-container thead {
  display: block;
  width: 100%;
}
.locations-container .table-container tbody {
  display: block;
  width: 100%;
  max-height: calc(100vh - 340px);
  height: 100%;
  min-height: 40px;
  overflow-y: auto;
  background-color: #FFFFFF;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.locations-container table>thead>tr>th {
  border-bottom: none;
}
.locations-container .table-container > div {
  overflow-x: auto;
  margin-top: 10px;
}
.locations-container td {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.locations-container table td div {
  overflow: hidden;
  text-overflow: ellipsis;
}
.locations-container table > tbody > tr:first-child > td {
  border-top: none;
}
.locations-container table th, .locations-container table td {
  padding: 80px;
  white-space: nowrap;
  margin: 0;
  border-top-width: 0;
}
.locations-container .table-container thead .header-cell {
  color: #9E9E9E;
  font-weight: normal;
}
.locations-container .table-container thead div.order-by {
  color: #424242;
  font-weight: bold;
}
.locations-container th.location-name, .locations-container td.location-name {
  width: 25%;
}
.locations-container th.address, .locations-container td.address {
  width: 60%;
}
.locations-container th.address .header-cell,
.locations-container th.stream-count .header-cell {
  margin-left: -7px;
}
.locations-container th.edit .header-cell {
  margin-left: -16px;
}
.locations-container th.stream-count, .locations-container td.stream-count {
  width: 120px;
  min-width: 120px;
  max-width: 120px;
}
.locations-container .table-container th.edit {
  text-align: center;
  width: 75px;
  min-width: 75px;
  max-width: 75px;
}
.locations-container .table-container td.edit {
  text-align: center;
  width: 60px;
  min-width: 60px;
  max-width: 60px;
}
.locations-container .locations .actions i {
  font-size: 14px;
}
.locations-container .pagination-controls {
  display:flex;
  justify-content: flex-end;
}
.locations-container dir-pagination-controls {
  margin-right: 50px;
}

@media screen and (min-width: 768px) {
  .locations-container .table-container tbody {
    max-height: calc(100vh - 290px);
  }
}
