/*** TRIAL EXPIRATION FORM ***/
.trial-expiration.form-container {
  position: fixed;
  width: 100%;
  height: 100%;
  max-height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1049;
  overflow: hidden;
}

.trial-expiration.form-container .account-creation-form {
  pointer-events: all;
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-overflow-scrolling: auto !important;
  padding-bottom: 40px;
}

.trial-expiration.form-container .logout-link {
  width: 80%;
  max-width: 800px;
  height: 40px;
  background: transparent;
  color: white;
  text-align: right;
  margin-left: auto;
  margin-right: auto;
}
.trial-expiration.form-container .logout-link.no-payment {
  max-width: 400px;
}
.trial-expiration.form-container .logout-link a {
  color: white;
  font-size: 1.6rem;
  line-height: 40px;
  cursor: pointer;
}
.trial-expiration.form-container .button-gap {
  min-height: 27px;
}
.trial-expiration.form-container .close-button,
.trial-expiration.form-container .back-button {
  position: relative;
  top: 34px;
  width: 85%;
  max-width: 760px;
  text-align: right;
  margin-left: auto;
  margin-right: auto;
}
.trial-expiration.form-container .close-button {
  color: #555555;
}
.trial-expiration.form-container .close-button i {
  font-size: 2.6rem;
  cursor: pointer;
}
.trial-expiration.form-container .back-button a {
  cursor: pointer;
}
.trial-expiration.form-container .form-step {
  width: 92%;
  max-width: 820px;
  background-color: #FFF;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 0;
}

.trial-expiration.form-container .form-step.no-payment {
  max-width: 400px;
}

.trial-expiration.form-container .form-header {
  padding: 0 30px 15px 30px;
  min-height: 50px;
}

.trial-expiration.form-container .step1 .form-header {
  border-bottom: 1px solid #ededed;
  color: #4a4a4a;
}

.trial-expiration h2 {
  font-size: 2rem;
  color: #4A4A4A;
  letter-spacing: 0;
}

.trial-expiration.form-container .form-header .title h1 {
  margin-bottom: 8px;
  font-size: 2.8rem;
}
.trial-expiration.form-container .form-header .title h2 {
  margin-top: 10px;
  margin-bottom: 21px;
  font-size: 1.3rem;
  color: #9b9b9b;
  padding-left: 0;
}

.trial-expiration.form-container .no-payment .form-header .title h2 {
  font-size: 1.6rem;
  line-height: 2rem;
}


.trial-expiration.form-container .step1 .form-header .title.convertingTrial h1 {
  color: #00c276;
}
.trial-expiration.form-container .form-header .title p {
  padding: 0;
  margin-bottom: 0;
}

.trial-expiration .plan-price-summary {
  font-size: 1.4rem;
  text-align: left;
  padding: 7px 10px;
  margin-bottom: 5px;
  color: #256379;
  background-color: #e7f9ff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 4px;
}
.trial-expiration .plan-price-summary .plan-price-total {
  text-align: right;
  color: #333;
}
.trial-expiration.form-container .billing-details-loading {
  border-bottom: 1px solid #EDEDED;
  padding: 35px;
}
.trial-expiration.form-container .billing-details-loading-content {
  min-height: 30px;
}
.trial-expiration.form-container .billing-details-loading h2 {
  margin-top: 0;
  padding: 0;
}
.trial-expiration.form-container .selected-plan {
  display: flex;
  flex-flow: row;
}
.trial-expiration.form-container .selected-plan .left-column {
  box-sizing: border-box;
  padding-right: 50px;
  width: 70%;
}
.trial-expiration.form-container .selected-plan .right-column {
  width: 30%;
}
.trial-expiration.form-container .selected-plan .change-plan {
  text-align: left;
  cursor: pointer;
  display: inline;
  margin-left: 10px;
}
.trial-expiration.form-container .selected-plan .change-plan a {
  background: #F2F2F2;
  border-radius: 3px;
  color: #4A4A4A;
  float: right;
  font-size: 1rem;
  padding: 5px 11px;
  white-space: nowrap;
}

.trial-expiration.form-container .step1 .selected-plan {
  color: #4a4a4a;
  text-align: left;
  padding: 15px 30px;
  border-bottom: 1px solid #ededed;
}

.trial-expiration.form-container .step1 .secure-icons {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 230px;
}
.trial-expiration.form-container .step1 .secure-icons .AuthorizeNetSeal {
  margin-right: 10px;
}
.trial-expiration.form-container .step1 .secure-icons .TrustWaveSeal {
  margin: 10px 25px 0 10px;
}
.trial-expiration.form-container h2,
.trial-expiration.form-container p {
  padding-left: 30px;
  padding-right: 30px;
}
.trial-expiration.form-container p {
  color: #888;
}
.trial-expiration.form-container .row {
  padding: 0 15px;
  margin: 15px 15px -15px 15px;
  display: block;
}
.trial-expiration.form-container .step1 .selected-plan {
  padding: 30px;
}
.trial-expiration .recurly-form .cancelable, .trial-expiration .recurly-form .billing-info {
  padding: 15px 5px 20px 5px;
  border-bottom: 1px solid #ededed;
}
.trial-expiration.form-container .total-charge {
  font-size: 1.4em;
  margin-top: 20px;
}
.trial-expiration.form-container .row .half.group {
  width: 100%;
}

.trial-expiration.form-container .row .split.half.group {
  display: flex;
  flex-flow: row nowrap;
  flex-direction: row;
}

.trial-expiration.form-container .billing .recurly {
  width: 100%;
}

.trial-expiration.form-container .row span.bar {
  width: 100%;
  position: relative;
  display: block;
}

.trial-expiration.form-container .row span.bar:before {
  left: 50%;
}

.trial-expiration.form-container .row span.bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: 1px;
  position: absolute;
  background: #1283c7;
  -webkit-transition: 0.2s ease all;
  transition: 0.2s ease all;
}

.trial-expiration.form-container input:focus ~ .bar:before,
.trial-expiration.form-container input:focus ~ .bar:after {
  width: 95%;
}

.trial-expiration.form-container input:focus:last-child ~ .bar:before,
.trial-expiration.form-container input:focus:last-child ~ .bar:after {
  width: 100%;
}

.trial-expiration.form-container div.recurly.focused ~ .bar:before,
.trial-expiration.form-container div.recurly.focused ~ .bar:after {
  width: 95%;
}

.trial-expiration.form-container div.recurly.focused:last-child ~ .bar:before,
.trial-expiration.form-container div.recurly.focused:last-child ~ .bar:after {
  width: 100%;
}

.trial-expiration.form-container select {
  border-radius: 4px;
}

.trial-expiration.form-container select:focus {
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
}

.trial-expiration.form-container .row span.error {
  color: #d9534f;
  font-size: 0.75rem;
}

.trial-expiration.form-container .group {
  position: relative;
}

.trial-expiration.form-container .group input {
  width: 100%;
}
.trial-expiration.form-container .group:last-child input {
  width: 100%;
}

.trial-expiration.form-container .coupon input:focus,
.trial-expiration.form-container .group input:focus {
  outline: none;
}

.trial-expiration .coupon input,
.trial-expiration .group input,
.trial-expiration .group textarea {
  background: #FFFFFF;
  border: 1px solid #CDCDCD;
  box-shadow: none;
  border-radius: 4px;
  font-size: 1.625rem;
  -webkit-rtl-ordering: logical;
  user-select: text;
  cursor: auto;
  font-weight: normal;
  padding: 5px 10px;
}

.trial-expiration .group input {
  height: 35px;
}

.trial-expiration .coupon label {
  display: block;
  font-size: 15px;
  color: #5D5D5D;
}
.trial-expiration .coupon label i {
  color: #D4D4D4;
  margin-right: 6px;
}

.trial-expiration .coupon label,
.trial-expiration .group label {
  color: #686868;
  font-size: 1.3rem;
  font-weight: normal;
}

.trial-expiration .coupon .coupon.status {
  margin-top: 4px;
  font-size: 1.1rem;
}
.trial-expiration .coupon .coupon.status .info {
  color: #00aa00;
}
.trial-expiration .coupon .coupon.status .coupon-error {
  color: #D9534F;
}

.trial-expiration .row .checkbox {
  margin: 0 20px;
}
.trial-expiration .checkbox-label {
  color: #555555;
  font-size: 1.625rem;
  font-weight: normal;
  padding-left: 10px;
  line-height: 2.25rem;
}

.trial-expiration input.invalid ~ label,
.trial-expiration select.invalid ~ label {
  color: #FF2222;
  font-weight: bold;
}
.trial-expiration.form-container .group input.invalid ~ span.bar,
.trial-expiration.form-container .group div.invalid ~ span.bar {
  width: 100%;
}
.trial-expiration.form-container .group input.invalid ~ span.bar:before,
.trial-expiration.form-container .group div.invalid ~ span.bar:before {
  left: 0;
}

.trial-expiration.form-container .group input.invalid ~ span.bar:after,
.trial-expiration.form-container .group div.invalid ~ span.bar:after {
  height: 2px;
  position: absolute;
  background: #FF2222;
}

.trial-expiration .group select {
  width: 100%;
  min-width: 100px;
  font-size: 1.625rem;
  background-color: transparent;
  border-color: #cdcdcd;
  height: 35px;
  color: #000;
}
.trial-expiration .step2 .group select {
  width: 95%;
}

.trial-expiration .row.button-container {
  min-height: 70px;
  position: relative;
  margin-bottom: 10px;
}
.trial-expiration .button-bar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
}
.trial-expiration .step2 .button-bar {
  justify-content: flex-end;
}
.trial-expiration .button-bar .buttons {
  margin-top: 10px;
  display: flex;
  justify-content: flex-end;
  width: 200px;
}
.trial-expiration .button-bar .buttons .button {
  max-width: 200px;
  height: 35px;
  font-size: 1.5rem;
  line-height: 1.5rem;
  padding: 7px 9px;
}

.trial-expiration .button-bar .loading {
  margin-left: 0;
  opacity: 0.75;
  width: 0;
}
.trial-expiration .button-bar #loading-image {
  width: 30px;
  height: 30px;
  left: -40px;
  top: 10px;
}

.trial-expiration .error {
  position: absolute;
  left: 0;
  top: 0;
  color: #d9534f;
  font-weight: bold;
  font-size: 1.35rem;
  line-height: 1.45rem;
  margin-left: 30px;
  width: 100%;
  min-width: 320px;
}
.trial-expiration .message {
  position: absolute;
  left: 0;
  top: 0;
  color: #7777FF;
  font-weight: normal;
  font-size: 1.35rem;
  line-height: 1.45rem;
  margin-left: 30px;
  width: 50%;
  min-width: 320px;
}

/*
 * Start of Plan Select page for Trial Expiration
 */

.trial-expiration.form-container .step3 .form-header {
  display: flex;
  flex-flow: row nowrap;
}
.trial-expiration.form-container .step3 .title {
  width: 50%;
  margin: 30px 0 0 0;
}
.trial-expiration.form-container .step3 .title h1 {
  font-size: 3rem;
  font-weight: 500;
  word-wrap: normal;
  line-height: 3.5rem;
  text-align: left;
  margin: 0;
  margin-bottom: 5px;
}
.trial-expiration .step3 .headerOptions {
  width: 50%;
  margin: 30px 0 0 0;
}
.trial-expiration .step3 .headerOptions .pricingOptions {
  display: flex;
  flex-flow: row nowrap;
  width: 300px;
  margin: 0 0 0 auto;
}
.trial-expiration .step3 .headerOptions .pricingOptions .planOp {
  border: 2px solid #ffffff;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.10);
  background: #fff;
  color: #4a4a4a;
  width:50%;
  font-size: 1.2rem;
  font-family: 'Lato', sans-serif;
  text-shadow: 0 1px 1px rgba(0,0,0,0.10);
  text-transform: uppercase;
  text-align: center;
  padding-top: 8px;
  padding-bottom: 7px;
  cursor: pointer;
}
.trial-expiration .step3 .headerOptions .pricingOptions .planOp.monthly {
  border-radius: 4px 0 0 4px;
  border: 2px solid #4a4a4a;
}
.trial-expiration .step3 .headerOptions .pricingOptions .planOp.annual {
  border-radius: 0 4px 4px 0;
  border: 2px solid #4a4a4a;
}
.trial-expiration .step3 .headerOptions .pricingOptions .planOp.selected {
  color: #fff;
  background: #4a4a4a;
}

.trial-expiration .step3 .planBreakdown {
  margin: 14px auto 0 auto;
  display: flex;
  flex-flow: row nowrap;
  max-width: 900px;
}
.trial-expiration .step3 .planBreakdown .button.moreInfo {
  cursor: pointer;
  color: #1283c7;
  font-size: 1.3rem;
  font-family: 'Lato', Helvetica, sans-serif;
  text-transform: capitalize;
  text-align: center;
  pointer-events: all;
  -webkit-transition: padding-bottom 1s;
  transition: padding-bottom 1s;
  text-transform: uppercase;
}
.trial-expiration .step3 .planBreakdown .button.moreInfo i {
  padding-right: 5px;
  pointer-events: none;
}
.trial-expiration .step3 .planBreakdown .button.moreInfo span {
  pointer-events: none;
}
.trial-expiration .step3 .planBreakdown .planColumn {
  max-width: 300px;
  margin: 0;
}
.trial-expiration .step3 .planBreakdown .planColumn .upperGap {
  height: 27px;
}
.trial-expiration .step3 .planBreakdown .planColumn .info {
  -webkit-transition: max-height 1s;
  transition: max-height 1s;
  overflow: hidden;
}
.trial-expiration .step3 .planBreakdown .planColumn .info .heading {
  color: #4a4a4a;
  font-size: 0.9375rem;
  font-weight: 700;
  padding-bottom: 5px;
}
.trial-expiration .step3 .planBreakdown .planColumn .info .deviceList {
  color: #4a4a4a;
  font-size: 0.75rem;
}
.trial-expiration .step3 .planBreakdown .planColumn .info .divider {
  width: 70%;
  margin: 23px auto;
  border-top: 2px solid #e2e2e2;
}
.trial-expiration .step3 .planBreakdown .planColumn .info ul.features {
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 18px;
}
.trial-expiration .step3 .planBreakdown .planColumn .info ul.features li {
  padding: 8px 0;
  margin: 0;
  font-size: 1.3rem;
  color: #4a4a4a;
  border-bottom: 2px solid #f5f5f5;
  text-align: center;
}
.trial-expiration .step3 .planBreakdown .planColumn .plan ul.features li:first-child {
  border-top: 2px solid #f5f5f5;
}
.trial-expiration .step3 .planBreakdown .planColumn .info .otherFeatures {
  font-weight: 700;
}
.trial-expiration .step3 .planBreakdown .planColumn.manage {
  z-index: 10;
}
.trial-expiration .step3 .planBreakdown .planColumn.manage .upperGap {
  background-color: #00c276;
  border-radius: 4px 4px 0 0;
  color: #fff;
  text-transform: uppercase;
  line-height: 27px;
  box-shadow: 0 8px 8px 3px rgba(0,0,0,0.08);
  text-align: center;
  font-size: 1.2rem;
}
.trial-expiration .step3 .planBreakdown .planColumn.manage .plan.manage {
  box-shadow: 0 8px 8px 3px rgba(0,0,0,0.08);
  border-radius: 0 0 4px 4px;
}
.trial-expiration .step3 .planBreakdown .plan {
  background-color: #fff;
  box-shadow: 0 3px 3px 0 rgba(0,0,0,0.10);
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  width: 100%;
  padding: 23px 35px;
  text-align: center;
  margin-bottom: 24px;
}
.trial-expiration .step3 .planBreakdown .plan .title {
  font-size: 2.6rem;
  font-weight: 700;
  color: #4a4a4a;
  margin: 0;
  margin-bottom: 0px;
  text-align: center;
  width: 100%
}
.trial-expiration .step3 .planBreakdown .plan .subtitle {
  font-size: 1.1rem;
  color: #9b9b9b;
  padding-bottom: 12px;
  border-bottom: 2px solid #f5f5f5;
  margin-bottom: 21px;
  text-transform: uppercase;
}
.trial-expiration .step3 .planBreakdown .plan .description {
  font-size: 1.4rem;
  color: #9b9b9b;
  margin-bottom: 21px;
}
.trial-expiration .step3 .planBreakdown .plan .price {
  margin-bottom: 5px;
}
.trial-expiration .step3 .planBreakdown .plan .price .dollarSign {
  font-size: 1.8rem;
  color: #bababa;
}
.trial-expiration .step3 .planBreakdown .plan .price .dollarAmount {
  font-size: 3.3rem;
  line-height: 3.3rem;
  font-weight: 700;
  vertical-align: top;
  color: #4a4a4a;
  padding: 0 5px;
}
.trial-expiration .step3 .planBreakdown .plan .price .changeAmount {
  font-size: 1.6rem;
  font-weight: 600;
  color: #4a4a4a;
}
.trial-expiration .step3 .planBreakdown .plan .billFrequency {
  text-transform: uppercase;
  font-size: 1.1rem;
  color: #bababa;
}
.trial-expiration .step3 .planBreakdown .plan .buttonBar {
  margin: 13px auto 22px auto;
}
.trial-expiration .step3 .planBreakdown .plan .buttonBar .select {
  text-transform: uppercase;
  background-color: #1283c7;
  border: 1px solid #1283c7;
  border-radius: 3px;
  color: #fff;
  width: 176px;
  padding: 5px 0;
  cursor: pointer;
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 600;
}
.trial-expiration .step3 .planBreakdown .plan.manage .buttonBar .select {
  border: 1px solid #00c276;
  background-color: #00c276;
  color: #fff;
}
.trial-expiration .step3 .planBreakdown .plan.music {
  border-radius: 4px 0 0 4px;
  border-right: 0;
}
.trial-expiration .step3 .planBreakdown .plan.messaging {
  border-radius: 0 4px 4px 0;
  border-left: 0;
}
.trial-expiration .step3 .planBreakdown .plan.other {
  border-radius: 4px;
  padding: 23px 19px;
}
.trial-expiration .step3 .planBreakdown .plan.other .subtitle {
  font-size: 0.875rem;
  color: #9b9b9b;
  margin-bottom: 20px;
}
.trial-expiration .step3 .planBreakdown .plan.other .description {
  margin-bottom: 15px;
}
.trial-expiration .step3 .planBreakdown .plan.other .description ul li {
  font-size: 0.9375rem;
  font-weight: 800;
  color: #4a4a4a;
  line-height: 26px;
}
.trial-expiration .step3 .planBreakdown .plan.other .buttonBar {
  margin-top: 20px;
}

@media all and (min-width: 850px) {
  .trial-expiration .step3 hr {
    display: none;
    margin: 0;
  }
  .trial-expiration .step3 .planBreakdown .plan {
    width: 250px;
  }
  .trial-expiration .step3 .planBreakdown .planColumn.manage .upperGap {
    margin-top: 0;
  }
  .trial-expiration .step3 div.form div.row {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-flow: row;
        -ms-flex-flow: row;
            flex-flow: row;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
       -moz-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  }
  .trial-expiration .step3 div.form div.row div.field {
    width: -webkit-calc(50% - 6px);
    width: calc(50% - 6px);
  }
  .trial-expiration .step3 div.form div.row div.field.message {
    width: 100%;
  }
}
@media all and (max-width: 850px) {
  .trial-expiration.form-container .group input {
    width: 100%;
  }
  .trial-expiration.form-container .step3 .headerOptions {
    width: 350px;
  }
  .trial-expiration .step3 .headerOptions .pricingOptions {
    margin: 0 auto;
  }
  .trial-expiration.form-container .step3 .form-header {
    display: block;
    text-align: center;
  }
  .trial-expiration.form-container .step3 .form-header .title {
    margin: 30px auto 0 auto;
    width: auto;
  }
  .trial-expiration.form-container .step3 .form-header .title h1 {
    text-align: center;
    font-size: 2.5rem;
    line-height: 2.625rem;
  }
  .trial-expiration.form-container .step3 .form-header .headerOptions {
    margin: 30px auto 0 auto;
  }
  .trial-expiration.form-container .step3 .form-header .headerOptions .enterprise.checkbox {
    text-align: center;
  }
  .trial-expiration .step3 .planBreakdown {
    display: block;
  }
  .trial-expiration .step3 .planBreakdown .plan.music {
    border-radius: 4px;
    border-right: 1px solid rgb(215,215,215);
  }
  .trial-expiration .step3 .planBreakdown .plan.messaging {
    border-radius: 4px;
    border-left: 1px solid rgb(215,215,215);
  }
  .trial-expiration .step3 .planBreakdown .planColumn {
    margin: 0 auto;
  }
  .trial-expiration .step3 .planBreakdown .planColumn.manage .upperGap {
    margin-top: 27px;
  }
}
@media all and (max-width: 500px) {
  .trial-expiration.form-container .step3 .headerOptions {
    width: 100%;
  }
}

.trial-expiration .pricingOptions .optionSet div.question {
  font-size: 2rem;
  line-height: 2.25rem;
  color: #2d2c2c;
  text-align: center;
  width: 60vw;
  margin: 10px auto;
  height: auto;
}

