.edit-location .content-form form {
  padding-left: 25px;
  padding-right: 25px;
}
.edit-location .content-form form input {
  max-width: 100%;
}
.edit-location button[type="submit"] {
  margin: 20px 15px;
}
.edit-location .content-form .form-group.flexed {
  justify-content: space-between;
  margin-bottom: 20px;
}
.edit-location .content-form .form-group.flexed:first-child {
  flex-direction: column;
}
.edit-location .content-form .location-permissions span, .edit-location .content-form .location-permissions .permissions {
  margin-left: 10px;
}
.edit-location .content-form .order-cloudbox {
  border-top: 1px solid #e6e6e6;
  padding-top: 10px;
  margin-top: 30px;
}
.edit-location .content-form .order-device {
  color: #666;
}
.edit-location .content-form .order-device-select {
  margin-top: 15px;
}
.edit-location .content-form .order-device-details {
  display: flex;
  flex-direction: column;
}
.edit-location .content-form .order-form-destination {
  margin-top: 10px;
  padding-top: 10px;
}
.edit-location .content-form .terms-container {
  border-top: 1px solid #e6e6e6;
  width: 100%;
}
.edit-location .content-form .terms {
  max-width: 95%;
  margin-top: 25px;
}
.edit-location .content-form .terms-container.ordering-box {
  border-top: none;
}
.edit-location .content-form .terms-container.ordering-box .terms {
  margin-top: 0;
}
.edit-location .content-form .order-form-destination .corp-address {
  margin-left: 20px;
}
.edit-location .content-form .breadcrumb>li+li:before {
  content: "";
}

.edit-location .content-form h1 {
  font-size: 2.4rem;
  margin-bottom: 20px;
  margin-top: 0;
}

.edit-location .content-form h1 > .fa {
  color: rgb(119, 119, 119);
  display: inline-block;
  font-size: 1.4rem;
  margin-left: 15px;
  vertical-align: middle;
}
.edit-location .content-form a:hover {
  text-decoration: none;
}
.edit-location .permissions-help-on-mobile {
  overflow-y: hidden;
}

@media screen and (max-width: 767px) {
  .edit-location .content-form .order-cloudbox {
    border-top: none;
    margin-top: 0;
  }
}

@media screen and (min-width: 600px) {
  .edit-location .content-form .order-device-details {
    flex-direction: row;
  }
  .edit-location .content-form .order-device .order-form-address {
    flex: 3;
  }
  .edit-location .content-form .order-device .order-form-checkout {
    flex: 2;
  }
}
@media screen and (min-width: 768px) {
  .edit-location .content-form form {
    max-width: 1000px;
    padding-left: 0;
    padding-right: 0;
  }
  .edit-location .content-form .order-device .order-form-address {
    flex: 5;
  }
  .edit-location .content-form .order-device .order-form-checkout {
    flex: 3;
  }
}
@media screen and (min-width: 968px) {
  .edit-location .content-form .terms {
    max-width: 70%;
    margin-bottom: 30px;
  }
}

