.default-container.billing-upgrade {
  min-height: 700px;
}
.default-container .billing-upgrade .row.plan-change {
  margin: 20px;
}
.billing-upgrade .plan-change-terms {
  margin-bottom: 20px;
}
.billing-upgrade .plan-change-terms ul {
  padding-left: 24px;
}
.billing-upgrade .plan-change-terms li {
  margin-bottom: 5px;
}
.billing-upgrade #manual-bill {
  margin: 10px 2px 0 5px;
}
.billing-upgrade label.manual-bill {
  margin-top: 10px;
  font-weight: normal;
}
.default-container .billing-upgrade h2.current-plan {
  margin-left: 0;
  font-size: 1.5rem;
  color: #1283C7;
}
.default-container .billing-upgrade h2, .default-container .billing-upgrade .discount {
  margin: 10px 0 10px 10px;
}
.default-container .billing-upgrade i {
  margin-right: 5px;
}
.default-container .billing-upgrade .current-plan {
  margin-left: 10px;
  color: #3366FF;
}
.billing-upgrade .headerOptions {
  margin: 30px 0 0 0;
}
.billing-upgrade .headerOptions .pricingOptions {
  display: flex;
  flex-flow: row nowrap;
  width: 300px;
  margin: 0 auto;
}
.billing-upgrade .headerOptions .pricingOptions .planOp {
  border: 2px solid #ffffff;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.10);
  background: #fff;
  color: #4a4a4a;
  width:50%;
  font-size: 1.2rem;
  font-family: 'Lato', sans-serif;
  text-shadow: 0 1px 1px rgba(0,0,0,0.10);
  text-transform: uppercase;
  text-align: center;
  padding-top: 8px;
  padding-bottom: 7px;
  cursor: pointer;
}
.billing-upgrade .headerOptions .pricingOptions .planOp.monthly {
  border-radius: 4px 0 0 4px;
  border: 2px solid #4a4a4a;
}
.billing-upgrade .headerOptions .pricingOptions .planOp.annual {
  border-radius: 0 4px 4px 0;
  border: 2px solid #4a4a4a;
}
.billing-upgrade .headerOptions .pricingOptions .planOp.selected {
  color: #fff;
  background: #4a4a4a;
}
.billing-upgrade .planBreakdown {
  margin: 14px auto 0 auto;
  display: flex;
  flex-flow: row nowrap;
  max-width: 900px;
}
.billing-upgrade .planBreakdown .moreInfo {
  cursor: pointer;
  color: #1283c7;
  font-size: 1.3rem;
  font-family: 'Lato', Helvetica, sans-serif;
  text-transform: capitalize;
  text-align: center;
  pointer-events: all;
  -webkit-transition: padding-bottom 1s;
  transition: padding-bottom 1s;
  text-transform: uppercase;
}
.billing-upgrade .planBreakdown .moreInfo.manage {
  color: #00c276;
}
.billing-upgrade .planBreakdown .planColumn {
  width: 300px;
  margin: 0;
}
.billing-upgrade .planBreakdown .planColumn .upperGap {
  height: 27px;
}
.billing-upgrade .planBreakdown .planColumn .plan ul.features {
  list-style-type: none;
  padding-left: 0;
  margin-bottom: 18px;
}
.billing-upgrade .planBreakdown .planColumn .plan ul.features li {
  padding: 8px 0;
  margin: 0;
  font-size: 1.3rem;
  color: #4a4a4a;
  border-bottom: 2px solid #f5f5f5;
}
.billing-upgrade .planBreakdown .planColumn .plan ul.features li:first-child {
  border-top: 2px solid #f5f5f5;
}
.billing-upgrade .planBreakdown .planColumn .plan ul.features .otherFeatures {
  font-weight: 700;
}
.billing-upgrade .planBreakdown .planColumn.manage {
  z-index: 10;
}
.billing-upgrade .planBreakdown .planColumn.manage .upperGap {
  background-color: #00c276;
  border-radius: 4px 4px 0 0;
  color: #fff;
  text-transform: uppercase;
  line-height: 27px;
  box-shadow: 0 8px 8px 3px rgba(0,0,0,0.08);
  text-align: center;
  font-size: 1.2rem;
}
.billing-upgrade .planBreakdown .planColumn.manage .plan.manage {
  box-shadow: 0 8px 8px 3px rgba(0,0,0,0.08);
  border-radius: 0 0 4px 4px;
}
.billing-upgrade .planBreakdown .plan {
  background-color: #fff;
  box-shadow: 0 3px 3px 0 rgba(0,0,0,0.10);
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  width: 100%;
  padding: 23px 35px;
  text-align: center;
  margin-bottom: 24px;
}
.billing-upgrade .planBreakdown .plan .title {
  font-size: 2.6rem;
  font-weight: 700;
  color: #4a4a4a;
  margin: 0;
  margin-bottom: 0px;
  text-align: center;
  width: 100%
}
.billing-upgrade .planBreakdown .plan .subtitle {
  font-size: 1.1rem;
  color: #bababa;
  padding-bottom: 12px;
  border-bottom: 2px solid #f5f5f5;
  margin-bottom: 21px;
  text-transform: uppercase;
}
.billing-upgrade .planBreakdown .plan .description {
  font-size: 1.4rem;
  color: #9b9b9b;
  margin-bottom: 21px;
}
.billing-upgrade .planBreakdown .plan .price {
  margin-bottom: 5px;
}
.billing-upgrade .planBreakdown .plan .price .dollarSign {
  font-size: 1.8rem;
  color: #bababa;
}
.billing-upgrade .planBreakdown .plan .price .dollarAmount {
  font-size: 3.3rem;
  line-height: 3.3rem;
  font-weight: 700;
  vertical-align: top;
  color: #4a4a4a;
  padding: 0 5px;
}
.billing-upgrade .planBreakdown .plan .price .changeAmount {
  font-size: 1.6rem;
  font-weight: 600;
  color: #4a4a4a;
}
.billing-upgrade .planBreakdown .plan .billFrequency {
  text-transform: uppercase;
  font-size: 1.1rem;
  color: #bababa;
}
.billing-upgrade .planBreakdown .plan .buttonBar {
  margin: 13px auto 22px auto;
}
.billing-upgrade .planBreakdown .plan .buttonBar .select {
  text-transform: uppercase;
  background-color: #1283c7;
  border: 1px solid #1283c7;
  border-radius: 3px;
  color: #fff;
  width: 176px;
  padding: 5px 0;
  cursor: pointer;
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 600;
}
.billing-upgrade .planBreakdown .plan.manage .buttonBar .select {
  border: 1px solid #00c276;
  background-color: #00c276;
  color: #fff;
}
.billing-upgrade .planBreakdown .plan.music {
  border-radius: 4px 0 0 4px;
  border-right: 0;
}
.billing-upgrade .planBreakdown .plan.messaging {
  border-radius: 0 4px 4px 0;
  border-left: 0;
}
.billing-upgrade .planBreakdown .plan.other {
  border-radius: 4px;
  padding: 23px 19px;
}
.billing-upgrade .planBreakdown .plan.other .subtitle {
  font-size: 0.875rem;
  color: #9b9b9b;
  margin-bottom: 20px;
}
.billing-upgrade .planBreakdown .plan.other .description {
  margin-bottom: 15px;
}
.billing-upgrade .planBreakdown .plan.other .description ul li {
  font-size: 0.9375rem;
  font-weight: 800;
  color: #4a4a4a;
  line-height: 26px;
}
.billing-upgrade .planBreakdown .plan.other .buttonBar {
  margin-top: 20px;
}

@media all and (min-width: 1000px) {
  .billing-upgrade hr {
    display: none;
    margin: 0;
  }
}
@media all and (max-width: 1000px) {
  .billing-upgrade .headerOptions {
    width: 350px;
    margin: 30px auto 0 auto;
  }
  .billing-upgrade .planBreakdown {
    display: block;
  }
  .billing-upgrade .planBreakdown .plan.music {
    border-radius: 4px;
    border-right: 1px solid rgb(215,215,215);
  }
  .billing-upgrade .planBreakdown .plan.messaging {
    border-radius: 4px;
    border-left: 1px solid rgb(215,215,215);
  }
  .billing-upgrade .planBreakdown .planColumn {
    margin: 0 auto;
  }
}
@media all and (max-width: 500px) {
  .billing-upgrade .headerOptions {
    width: 100%;
  }
}

.billing-upgrade .have-questions {
  text-align: center;
  color: #4a4a4a;
  line-height: 2rem;
  margin-top: 10px;
  margin-bottom: 30px;
}
.billing-upgrade .have-questions .question {
  font-size: 1.4rem;
}
.billing-upgrade .have-questions .phone-number {
  font-size: 1.6rem;
}

.default-container .billing-upgrade h2 {
  margin: 0;
  font-size: 1.6rem;
  color: #353535;
  font-weight: 600;
  margin-top: 22px;
}
.billing-upgrade .plan-details table {
  margin: 13px 0;
  width: 100%;
  border-spacing: 0;
}
.billing-upgrade .plan-details table th {
  border-bottom: none;
  padding: 10px;
}
.billing-upgrade .current.plan-details table th {
  border: 1px solid #f1f1f1;
  background: #fafafa;
}
.billing-upgrade .selected.plan-details table th {
  border: 1px solid #c9dee7;
  background: #deeff8;
}
.billing-upgrade .plan-details table td {
  border: 1px solid #f1f1f1;
  padding: 10px;
  vertical-align: top;
}
.billing-upgrade .plan-details table .plan {
  width: 70%;
}
.billing-upgrade .plan-details table .plan .text-success {
  color: #5CB85C;
}
.billing-upgrade .plan-details table .price {
  width: 10%;
  min-width: 120px;
  border-left: none;
}
.billing-upgrade .plan-details table .locations {
  width: 10%;
  min-width: 87px;
  border-left: none;
  border-right: none;
}
.billing-upgrade .plan-details table .total {
  width: 10%;
  min-width: 150px;
}
.billing-upgrade .plan-details table th.plan {
  border-radius: 5px 0 0 0;
}
.billing-upgrade .plan-details table th.price,
.billing-upgrade .plan-details table th.locations {
  border-radius: 0;
}
.billing-upgrade .plan-details table th.total {
  border-radius: 0 5px 0 0;
}
.billing-upgrade .plan-details table td.plan {
  /* border-radius: 0 0 0 5px; */
}
.billing-upgrade .plan-details table td.price,
.billing-upgrade .plan-details table td.locations {
  border-radius: 0;
}
.billing-upgrade .plan-details table td.total {
  /* border-radius: 0 0 5px 0; */
}
.billing-upgrade .plan-details tr.totalWithAddons td.empty {
  border: none;
}
.billing-upgrade .plan-details tr.totalWithAddons td.total {
  background-color: #fafafa;
}
.billing-upgrade .billing-info {
  margin: 32px 0 28px 0;
  font-size: 1.5rem;
  color: #353535;
  line-height: 21px;
}
.billing-upgrade .submit-spinner {
  color: #1083c7;
  font-size: 2rem;
  margin: 8px;
}
.modal .modal-body.downgrade h2 {
  font-size: 2.4rem;
}
.modal .modal-body.downgrade p {
  margin: 5px 0 8px 0;
}
.modal .modal-body.downgrade p strong {
  font-family: 'Lato-bold', sans-serif;
}
.modal .modal-body.downgrade p:last-child {
  margin-bottom: 0;
}
