.admin-toolbar {
  background-color: #fafafa;
}
.admin-toolbar.left-right {
  position: absolute;
  display: flex;
  flex-direction: column-reverse;
  align-items: left;
  justify-content: center;
  background-color: #fafafa;
  max-height: 90px;
  padding: 8px;
  left: 7px;
  top: -50px;
  width: calc(100% - 10px);
  z-index: 50;
}
/*
.admin-toolbar.left-right .mass-nav {
  margin-top: 10px;
}
*/
.admin-toolbar .left-side {
  display: flex;
  align-items: center;
  min-width: 320px;
  margin-top: 10px;
}
.admin-toolbar .left-side.empty {
  margin-top: 0;
}
.admin-toolbar .right-side {
  display: flex;
  justify-content: flex-start;
  min-height: 34px;
  margin-left: 59px;
}
.admin-toolbar .left-side label {
  margin-bottom: 0;
  width: 120px;
}
.admin-toolbar .upload-btn {
  margin-right: 10px;
}
.admin-toolbar .btn i {
  margin-right: 3px;
}
.admin-toolbar .filter {
  width: calc(100% - 132px);
  max-width: 300px;
  min-width: 200px;
  display: inline;
  vertical-align: bottom;
  margin-left: 8px;
  margin-right: 8px;
}
.mobile .admin-toolbar .filter {
  width: 150px;
}
@media screen and (max-width: 360px) {
  .mobile .admin-toolbar .filter {
    width: 115px;
  }
}

.admin-toolbar .select2 .select2-container .select2-selection--single {
  height: 34px;
}
.admin-toolbar .select2 .select2-container--default .select2-selection--single .select2-selection__rendered {
  line-height: 32px;
}

/*  Having the following unique settings for placement of toolbar is Not Smart
 *  Let's come up with a different way of doing this.
 */
/* unique settings for locations page */
.locations .admin-toolbar.left-right {
  left: 25px;
}
.locations .admin-toolbar .right-side {
  margin-left: 38px;
  min-width: 0;
}

/* unique settings for regions page*/
.regions .admin-toolbar.left-right {
  top: -110px;
}
.regions .admin-toolbar .right-side {
  margin-left: 60px;
  min-width: 0;
}


/* unique settings for stream history page */
.streamed-container .admin-toolbar .right-side {
  min-width: 0;
}
.streamed-container .admin-toolbar .left-side {
  margin-top: 10px;
  min-width: 230px;
}

@media screen and (min-width: 768px) {
  .admin-toolbar.left-right,
  .locations .admin-toolbar.left-right,
  .admins-container .admin-toolbar.left-right,
  .mixes-container .admin-toolbar.left-right,
  .dayparting-container .admin-toolbar.left-right,
  .streamed-container .admin-toolbar.left-right,
  .regions .admin-toolbar.left-right {
    width:100%;
    position: relative;
    align-items: flex-start;
    background-color: #fafafa;
    top: 0;
    left: 0;
  }
  .admin-toolbar .left-side {
    width: 50%;
  }
  .streamed-container .admin-toolbar.left-right {
    flex-direction: row;
  }
  .streamed-container .admin-toolbar .left-side {
    margin-top: 0;
    min-width: 246px;
  }
  .streamed-container .admin-toolbar .right-side input {
    min-width: 200px;
  }

  .admin-toolbar .right-side {
    justify-content: flex-start;
    margin-left: 0;
  }
  .locations .admin-toolbar .right-side, .regions .admin-toolbar .right-side {
    margin-left: 0;
  }
  .admin-toolbar .selectGroup1 > select, .admin-toolbar .selectMusic1 > select {
    width: 60px;
  }
  .admin-toolbar .selectPreset1 > select .admin-toolbar .selectSchedule1 > select {
    width: 120px;
  }
}
@media screen and (min-width: 970px) {
  .admin-toolbar.left-right {
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    height: 50px;
    margin-bottom: 0;
  }
  .streamed-container .admin-toolbar .left-side,
  .admin-toolbar .left-side {
    margin-top: 0;
  }
  .admin-toolbar .right-side {
    justify-content: flex-end;
  }
  .admin-toolbar .filter {
    width: 200px;
    margin-right: 0;
  }
  .admin-toolbar.left-right .mass-nav {
    margin-top: 0;
  }
}


