/*Logged out state style*/
.logout-container {
  font-family: 'Lato';
  margin-top: 0;
}
.logout-container .container {
  min-width: auto;
}
.logout-container .cloudanimation {
  position: absolute;
  height: 516px;
  width: 100%;
  background-position: 0px 0px;
}
.logout-container .logout-header-wrapper {
  padding-top: 30px;
  position: relative;
  width: calc(100vw - 40px);
  margin: 0 auto 20px auto;
}
.logout-container .logout-header-wrapper .logo {
  background: url("../images/login-logo.png") no-repeat;
  background-size: 370px 83px;
  width: 370px;
  height: 83px;
  margin: 40px auto;
}

.login-background {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(-154deg, #0CACE4 0%, #1957A7 100%);
  background-color: #0CACE4;
  height: 100vh;
}

/*Login container*/
.login-container {
  width: 420px;
  margin: 0px auto;
  position: relative;
  /* The white background content wrapper */
}

.login-container.duo-container {
  max-width: 700px;
  width: auto;
}

.login-container .row {
  margin: 0;
  background: #FAFAFA;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.20), 0 1px 3px 0 rgba(0,0,0,0.10);
  border-radius: 5px;
}

.login-container .block-unit {
  background-color: transparent;
  padding: 25px;
}

.login-container .links {
  color: #455d72;
  z-index: 3;
  margin-top: 25px;
  height: 110px;
  padding-left: 15px;
  border-left: 1px solid #efefef;
}

.login-container .api-status-message {
  border: 1px solid #ffddaa;
  border-radius: 4px;
  background-color: #ffffff;
  color: #777;
  padding: 15px;
  font-size: 1.55rem;
}
.login-container .api-version, .admin-block-unit .api-version {
  font-size: 8px;
  color: #888;
}

.app-login-form form {
  width: 100%;
}

.app-login-form form input:focus {
  outline: none;
  border-color: transparent;
  box-shadow: none;
}

.app-login-form form div.form-group {
  background: #FFFFFF;
  border: 1px solid #EDEDED;
  border-radius: 5px;
  display: flex;
  flex-flow: row nowrap;
  height: 45px;
  vertical-align: middle;
  margin: 15px 0;
}

.app-login-form form div.username {
  margin-top: 12px;
}

.app-login-form form div.lost-password {
  padding: 10px 0 25px 0;
}

.app-login-form form a, .password-reset-form label {
  font-size: 15px;
  color: #8E8E8E;
  margin: 10px 0;
}

.app-login-form form input {
  border: 0px solid transparent;
  background: transparent;
  color: #888888;
  font-size: 15px;
  margin-top: auto;
  margin-bottom: auto;
  box-shadow: none;
}

.app-login-form form .icon {
  font-size: 20px;
  color: #B6B6B6;
  margin: 0 15px;
  padding-top: 12px;
}

.app-login-form form input.ng-invalid {
}

.app-login-form form textarea.ng-invalid {
}
.app-login-form form input.ng-pristine {
}
.app-login-form form input.ng-valid {
}

.app-login-form form button.btn-success,
.password-reset-form form button {
  background: #00C276;
  border: 1px solid #00AB68;
  border-radius: 5px;
  width: 370px;
  height: 45px;
  font-size: 15px;
  color: #FFFFFF;
  margin-bottom: 12px;
}

.password-retrieval-form form button.signin {
  background: #00C276;
  border: 1px solid #00AB68;
}
.password-reset-form form button {
  width: 100%;
}

.logout-container .api-version {
  margin-top: 11px;
  font-size: 10px;
  color: #FFFFFF;
}

.logout-container .tag-line {
  font-family: 'Raleway';
  font-size: 24px;
  color: #FFFFFF;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 100px;
}

.logout-container .oldapp {
  width: 116px;
  height: 24px;
  padding: 3px 0 0 8px;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  border-radius: 5px;
  font-family: Lato;
  font-size: 11px;
  color: #4A4A4A;
}

.logout-container .oldapp i {
  pointer-events: none;
}

.logout-container .copyright {
  position: fixed;
  display: block;
  width: 100%;
  bottom: 0;
  padding-bottom: 30px;
  font-family: 'Lato';
  font-size: 15px;
  color: white;
  text-align: center;
}

.password-retrieval-form label, .password-reset-form h4 {
  font-size: 18px;
  color: #8E8E8E;
  margin-bottom: 10px;
  font-weight: 500;
}

.password-reset-form label {
  font-weight: 400;
}

.password-retrieval-form .button-bar {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: 10px 0 20px 0;
}

.password-retrieval-form button.signin {
  width: 48%;
}

.password-retrieval-form button.back {
  width: 48%;
  background-color: #ddd;
  border-color: #c7c7c7;
}

.password-retrieval-form .contact-support {
  color: #8e8e8e;
  font-size: 15px;
}

.sonos h1 {
  color: white;
  text-align: center;
  font-size: 70px;
  position: relative;
}

.sonos p.sonos-blurb {
  color: white;
  display: block;
  width: auto;
  max-width: 380px;
  margin: 15px auto;
}

.sonos .login-container {
  margin-bottom: 100px;
  max-width: 380px;
}

.sonos .login-container .validation-error {
  margin: 0 0 30px;
}

/* Override */
.sonos .login-container img.loader {
  display: initial;
}

.sonos .app-login-form form div.form-group {
  height: auto;
}

.sonos .app-login-form form .button-bar,
.sonos .app-login-form form .lost-password {
  text-align: center;
}

.sonos .app-login-form form .button-bar button {
  margin-top: 15px;
  width: 200px;
}

#duo-container {
  width: 100%;
}

#duo-iframe {
  border: none;
  height: 350px;
  width: 100%;
}

@media screen and (max-height: 700px) {
  .logout-container .copyright {
    position: relative;
  }
}

@media screen and (max-width: 500px) {
  .logout-container .logout-header-wrapper .logo {
    width: 232px;
    height: 52px;
    background-size: 232px 52px;
  }
  .logout-container .container {
    width: calc(100vw - 40px);
    min-width: auto;
  }
  .logout-container .login-container {
    width: 100%;
  }
  .app-login-form form button,
  .app-login-form form button.btn-success {
    width: 100%;
  }
  .logout-container .copyright {
    font-size: 12px;
  }
}
