.account-content-container .account-content-view .report .row {
  margin: 0 -15px;
}

@media (max-width: 767px) {
  .reports {
    padding: 0 15px;
  }
}

/* Override `main.css` */
.reports .col-lg-6,
.reports .col-md-8,
.reports .col-sm-12,
.reports .col-sm-6 {
  margin-bottom: 0;
}

.reports-desc {
  font-size: 16px;
  margin-bottom: 20px;
}

.report-section-header {
  margin-bottom: 10px;
}

.report-section-header .h4 {
  margin-bottom: 5px;
}

.reports .report .checkbox label {
  font-weight: 400;
}

.reports .timezone-form-group .control-label {
  padding-right: 15px;
  padding-top: 5px;
  vertical-align: top;
}

.reports .timezone-form-group .form-control {
  display: inline-block;
  width: auto;
}

.reports .timezone-form-group .form-control + .fa {
  margin-left: 15px;
}

.reports .report {
  border-top: 1px solid #EEE;
  margin-top: 30px;
  padding-top: 30px;
}

.reports .report:last-child {
  border-bottom: none;
}

.reports .report h2 .fa-push-right {
  margin-right: 10px;
}

.reports .report .h3 {
  font-size: 22px;
  margin-top: 0;
}

.reports .report .h4 {
  font-size: 16px;
  margin-top: 15px;
}

.reports .report .panel {
  margin-bottom: 0;
}

.reports .report .report-loading {
  display: inline-block;
  vertical-align: middle;
}

.reports .report .report-loading .fa-push-left {
  margin-left: 10px;
}

.reports .report fieldset legend {
  border-bottom: none;
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 5px;
}

@media (min-width: 768px) {
  .reports .report .status-form-group {
    display:inline-block;
    margin-left: 50px;
  }
}

.automated-reports {
  border-left: 1px solid #DDD;
  padding: 0 0 30px 20px;
}

.automated-report-opt-list {
  margin: 0 0 15px;
}

.automated-report-opt-list .checkbox:first-child {
  margin-top: 0;
}

.automated-report-opt-list .checkbox:last-child {
  margin-bottom: 0;
}

.automated-report-opt .fa {
  margin-left: 10px;
}

@media (min-width: 768px) {
  .automated-report-opt-list .btn-link.add-recipients {
    border: none;
    padding: 0;
    vertical-align: baseline;
  }
}

.reports .report-add-recipients {
  margin: 10px 0;
}

.reports .report .location-picker {
  margin-top: -30px;
  margin-left: 15px;
}

/* Tweak the dropdown inside the nya-bs-select component */
.reports .report .dropdown-menu {
  position: relative;
  top: 0px;
}
