.scheduler-popup {}
  .scheduler-popup .station-selection {
    margin-top: 16px;
    border: 1px solid #262626;
    border-radius: 5px; }
  .scheduler-popup .station-selection .select-button {
    width: 50%;
    display: inline-block;
    cursor: pointer;
    padding: 5px;
    font-size: 1.5rem;
    letter-spacing: 1px;
    text-align: center;
    color: #262626;
  }
  .scheduler-popup .station-selection .mix-station.select-button {
    float: right; }
  .scheduler-popup .station-selection .select-button.selected {
    background-color: #262626;
    color: white; }
  .scheduler-popup .row {
    margin-left: 0;
    margin-right: 0; }
  .scheduler-popup .row select {
    height: 30px;
    margin-left: 5px; }
  .scheduler-popup .row label {
    width: auto; }
  .scheduler-popup .row .station {
    width: 100%;
    margin: 15px 0; }
  .scheduler-popup .row .station.disabled button {
    pointer-events: none;
    color: #666666;
  }
  .scheduler-popup .row .station.disabled .caret {
    display: none;
  }
  .scheduler-popup .row .time {
    width: 90px; }
  .scheduler-popup .row .left {
    display: inline-block;
    width: 49%;
    text-align: left; }
  .scheduler-popup .row .right {
    display: inline-block;
    width: 49%;
    text-align: right; }
  .scheduler-popup .button-bar {
    position: relative;
    display: flex;
    justify-content: flex-end; }
  .scheduler-popup .btn.submit {
    padding: 6px 16px;
    border-radius: 5px;
    margin-top: 14px;
    text-align: center; }
  .scheduler-popup label {
    width: 25%; }
  .scheduler-popup select {
    width: 70%;
    margin-left: 10px; }

.scheduler-popup .row {
  margin-top: 10px;
}
.scheduler-popup .row label {
  width: auto;
}
.scheduler-popup .row select.message {
  margin-left: 0;
}
.scheduler-popup .row .date-field {
  border: 1px solid #A6A6A6;
  border-radius: 4px;
  height: 30px;
  width: 94px;
  line-height: 30px;
  text-align: center;
  display: inline-block;
  margin-right: 8px;
  vertical-align: bottom;
}
.scheduler-popup .row .time {
  border: 1px solid #A6A6A6;
  border-radius: 4px;
  height: 30px;
  vertical-align: bottom;
}
.scheduler-popup .row select.ampm {
  width: 50px;
}

.scheduler-popup .row select.repeat {
  margin-left: 0;
}
.scheduler-popup .row .repeat-options {
  margin-top: 20px;
  margin-bottom: 20px;
}

.scheduler-popup week-selector {
  display: inline-block;
}
.scheduler-popup week-selector div {
  display: inline-block;
  width: 35px;
  height: 35px;
  border: 1px solid #b6b6b6;
  border-right: none;
  text-align: center;
  line-height: 35px;
  cursor: pointer;
  font-size: .9em;
}
.scheduler-popup week-selector div:last-child {
  border-right: 1px solid #b6b6b6;
}
.scheduler-popup week-selector div.selected {
  background-color: #e6e6e6;
}

.scheduler-popup .row select.end-repeat {
  width: 80px;
  margin-right: 10px;
}
.scheduler-popup .row.scope-selector div {
  border-bottom: 1px solid #e7e7e7;
}
.scheduler-popup .button-bar {
  border-top: 1px solid #e7e7e7;
  margin-top: 15px;
}
.scheduler-popup .row .afterPlays {
  display: inline-block;
  vertical-align: top;
}
.scheduler-popup .row .afterPlays input {
  width: 50px;
  height: 30px;
  border-radius: 4px;
  border: 1px solid #A6A6A6;
  vertical-align: middle;
  margin-right: 8px;
}
.scheduler-popup .row .digits {
  text-align: center;
}
.scheduler-popup .row .onDate {
  display: inline-block;
  vertical-align: middle;
}
.modal .modal-dialog .scheduler-popup .daypart-edit-form .dropdown-menu ul.inner {
  max-height: 60vh;
  min-height: 94px;
  overflow-y: auto;
}
.date-picker-popup {
  position: absolute;
  z-index: 100;
}
.modal.message-scheduler .modal-dialog h2 {
  padding-bottom: 18px;
}

.modal.message-scheduler .modal-dialog .input-dropdown {
  vertical-align: middle;
}
.modal.message-scheduler .modal-dialog .input-dropdown ul {
  max-height: 170px;
  width: 60px;
  overflow-y: scroll;
}
.modal.message-scheduler .modal-dialog .input-dropdown ul > li {
  text-align: center;
  padding: 3px;
}
.modal.message-scheduler .modal-dialog .input-dropdown ul > li.active {
  background-color: #dfdfdf;
}
.modal.message-scheduler .modal-dialog .input-dropdown ul > li:hover {
  background-color: #efefef;
}
.modal.message-scheduler .modal-dialog input.time {
  width: 40px;
  text-align: center;
}
.modal.message-scheduler .modal-dialog .date-field {
  width: 95px;
}
.modal.message-scheduler .modal-dialog select.ampm {
  vertical-align: middle;
  width: 60px;
}
.modal.message-scheduler .modal-dialog select.message,
.modal.message-scheduler .modal-dialog select.repeat {
  width: 265px;
}
.modal.message-scheduler .modal-dialog select.end-repeat {
  width: 90px;
  margin-left: 0;
}

.modal.message-scheduler .scheduler-popup .row label {
  width: 80px;
}

.modal.message-scheduler .scheduler-popup .btn-delete {
  position: absolute;
  font-weight: 400;
  font-size: 1.4rem;
  color: #999;
  left: 0;
  top: 20px;
  cursor: pointer;
}
.modal.message-scheduler .day-prompt {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  color: orange;
}
