
.content-ratings-explanation-popup .content-ratings-explanation-content h2 {
  margin-bottom: 10px;
}

.content-ratings-explanation-content .content-rating-icon {
  height: 21px;
  width: 35px;
}

.content-ratings-explanation-content .content-rating-icon .fg {
  border: 1px solid;
  font-size: 1.25rem;
}

.content-ratings-explanation-content .content-ratings-list > li {
  position: relative;
}

.content-ratings-explanation-content .content-ratings-list > li .content-rating-icon {
  left: 0;
  position: absolute;
  top: 4px;
}

.content-ratings-explanation-content .content-ratings-list > li p {
  font-size: 14px;
  margin-bottom: 20px;
  padding-left: 45px;
}

.content-ratings-explanation-content .content-ratings-list > li:last-child p {
  margin-bottom: 0;
}
