/* Message Preset Page */

/* Override */
.message-preset-container  .row > .col-sm-6,
.message-preset-container  .row > .col-sm-12,
.message-preset-container  .row > .col-md-8,
.message-preset-container  .row > .col-lg-6 {
  margin-bottom: 0;
}

.message-preset-container .message-preset-form-header {
  margin-bottom: 30px;
}

@media (min-width: 1200px) {
  .message-preset-container .message-preset-form-header {
    margin-bottom: 10px;
  }

  .message-preset-container .message-preset-form-header .checkbox-inline {
    margin-top: 7px;
  }
}

.message-preset-container .message-preset-form-title {
  color: #1283C7;
  border-bottom: 2px solid #1283C7;
  font-size: 14px;
  letter-spacing: 1px;
  margin-bottom: 20px;
  margin-top: 0;
  padding-bottom: 6px;
  text-transform: uppercase;
}

.message-preset-container .message-search {
  position: relative;
}

.message-preset-container .message-search .form-control {
  padding-left: 32px !important;
}

.message-preset-container .message-search .fa {
  color: #777;
  font-size: 13px;
  left: 12px;
  position: absolute;
  top: 10px;
}

.message-preset-container .message-frequency {
  margin: 0 0 15px;
}

.message-preset-container .message-frequency .control-label {
  margin-right: 10px;
}

.message-preset-container .message-frequency .form-control {
  min-width: 100px;
}

.message-preset-container .toolbar-container {
  display: inline-block;
  width: 100%;
}
.message-preset-container form .error, .daypart-edit-screen form .error {
  padding-left: 5px;
  color: #d9534f;
}

.message-preset-container .playlist-list,
.message-preset-container .added-messages {
  margin-bottom: 30px;
}

.message-preset-container .playlist-list {
  max-height: 375px;
  overflow-y: auto;
}
.message-preset-container .playlist-list .list-group-item {
  width: 100%;
  text-align: left;
}
.message-preset-container button.list-group-item .message-preset-action {
  width: 18px;
  display: block;
  color: #777;
  margin-top: 2px;
  margin-right: 5px;
  text-align: center;
}
.message-preset-container button.list-group-item:hover {
  background-color: #EAF8FF;
  color: #555;
}
.message-preset-container button.list-group-item .message-preset-action .fa {
  font-size: 1.8rem;
}
.message-preset-container button.list-group-item:hover .message-preset-action {
  color: #1283C7;
}

.message-preset-container button.list-group-item .message-preset-action .before-add {
  display: block;
}
.message-preset-container button.list-group-item .message-preset-action .after-add {
  display: none;
}
.message-preset-container button.list-group-item.message-added .message-preset-action .after-add {
  display: block;
}
.message-preset-container button.list-group-item.message-added .message-preset-action .before-add {
  display: none;
}
/*
.message-preset-container button.list-group-item.message-added {
  pointer-events: none;
  transition: opacity 200ms linear;
}
*/

.message-preset-container button.list-group-item:active {
  background-color: #d5d5d5;
}

.message-preset-container .added-messages .live-messages .part-block {
  border-bottom: 1px solid #DDD;
  padding: 6px 12px;
  display: block;
}

.message-preset-container .added-messages .live-messages .part-block:last-child {
  border-bottom: none;
}

.message-preset-container .added-messages .live-messages ul[dnd-list] .dndDraggingSource {
  display: none;
}

.message-preset-container .added-messages .live-messages ul[dnd-list] .dndPlaceholder {
  display: block;
  background-color: #ddd;
  min-height: 42px;
}

.message-preset-container .added-messages .live-messages ul[dnd-list] li {
  display: block;
  padding: 10px 15px;
  margin-bottom: -1px;
  cursor: pointer;
}

.message-preset-container .added-messages .live-messages ul[dnd-list] li .drag-handle {
  color: #777777;
  margin-right:20px;
}

.account-content-container .account-content-view .message-preset-container.row {
  margin-left: -15px;
  margin-right: -15px;
}
.message-preset-container .added-messages .no-selected-messages {
  color: #777;
  padding: 60px 0;
  text-align: center;
}
.message-preset-container .added-messages .no-selected-messages .fa {
  display: block;
  margin-bottom: 10px;
}
.message-preset-container .search-results-empty {
  color: #777;
  margin: 30px 0 0;
  padding: 15px 0;
  text-align: center;
}
.message-preset-container .search-results-empty .fa {
  display: block;
  font-size: 26px;
  margin-bottom: 10px;
}

.message-preset-container .remove-message-btn {
  font-size: 20px;
  margin-top: 1px;
  padding-bottom: 0;
  padding-top: 0;
}

.message-preset-container .remove-message-btn:hover,
.message-preset-container .remove-message-btn:focus {
  text-decoration: none;
}