.volume-slider .tail {
  position: relative;
  background: #efefef;
  margin-top: -1px;
}

.volume-slider .tail:after, .volume-slider .tail:before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.volume-slider .tail:before {
  border-top-color: #dedede;
  border-width: 9px;
  margin-left: 78px;
  margin-top: 6px;
}

.volume-slider .tail:after {
  border-top-color: #ffffff;
  border-width: 8px;
  margin-left: 79px;
  margin-top: 5px;
}

