    bottom: 140px;
  }
  .col-xs-10 {
    width: auto;
    max-width: 490px;
  }
  .account-playlists .created-by {
    display: none;
  }
}

@media screen and (min-width: 701px) and (max-width: 900px) {
  .growl {
    right: 160px;
    width: calc(100vw - 320px);
    bottom: 140px;
  }
  .col-xs-10 {
    width: 77%;
    max-width: 800px;
  }
}

@media screen and (min-width: 901px) and (max-width: 1100px) {
  .col-xs-10 {
    width: 78%;
    max-width: 900px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1100px) {
  .admin-block-unit .admin-content.col-xs-10 {
    width: calc(100% - 180px);
  }
}

@media screen and (max-width: 800px) {
  .registration-top-bar .registration-top-bar-title {
    margin-top: -20px;
  }
}

