.message-scheduler-popup-toolbar {
  position: absolute;
  transform: translateX(-50%);
  z-index: 1000;
}
.message-scheduler-popup-toolbar .header {
  background: #555;
  color: #fff;
  padding: 5px 8px;
  border-radius: 5px 5px 0 0;
}
.message-scheduler-popup-toolbar .body {
  position: relative;
  padding: 5px;
}
.message-scheduler-popup-toolbar .body .button-bar {
  position: absolute;
  right: 0;
  top: 0;
  /* after exceptions ready
  width: 70px;
  */
  width: 33px;
}
.message-scheduler-popup-toolbar .body .button-bar .btn {
  display: inline-block;
  width: 33px;
  height: 33px;
  text-align: center;
  padding: 6px;
}
.message-scheduler-popup-toolbar .edit-button {
  text-transform: none;
  float: left;
}
.message-scheduler-popup-toolbar .delete-button {
  float: right;
}
.message-scheduler-popup-toolbar .row {
  position: relative;
  background: transparent;
  color: #555;
  margin: 0;
  width: 100%;
  font-weight: 400;
  padding: 2px 4px;
}
.message-scheduler-popup-toolbar .row span.date {
  float: left;
}
.message-scheduler-popup-toolbar .row span.recur-icon {
  padding-left: 10px;
}

.message-scheduler-popup-toolbar .button {
  text-transform: uppercase;
  padding: 5px;
}
.message-scheduler-popup-toolbar .toolbar {
  background-color: #fefefe;
  color: #444;
  padding: 0;
  border: 1px solid #dedede;
  box-shadow: 1px 1px 1px #ddd;
  border-radius: 5px;
}
.message-scheduler-popup-toolbar .toolbar .button {
  cursor: pointer;
}
.message-scheduler-popup-toolbar .tail {
  position: relative;
  background: #efefef;
  margin-top: -1px;
}
.message-scheduler-popup-toolbar .tail:after, .message-scheduler-popup-toolbar .tail:before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
}

.message-scheduler-popup-toolbar .tail:after {
  border-color: rgba(223, 223, 223, 0);
  border-top-color: #efefef;
  border-width: 8px;
  margin-left: -8px;
}
.message-scheduler-popup-toolbar .tail:before {
  border-color: rgba(222, 222, 222, 0);
  border-top-color: #dedede;
  border-width: 9px;
  margin-left: -9px;
}
