.tri-toggle {
  display: inline-block;
}
.tri-toggle label {
  padding: 0 3px;
  cursor: pointer;
}
.tri-toggle .tri-toggle-wrap {
  position: relative;
  display: inline-block;
  background-color: #fcfcfc;
  border: 1px solid #777;
  border-radius: 7px;
  height: 15px;
  width: 30px;
  vertical-align: text-bottom;
  cursor: pointer;
}
.tri-toggle .tri-toggle-wrap.disabled {
  opacity: 0.4;
  pointer-events: none;
}
.tri-toggle .tri-toggle-handle {
  position: absolute;
  display: inline-block;
  pointer-events: none;
  vertical-align: top;
  background-color: #fafafa;
  border: 1px solid #777;
  border-radius: 7px;
  height: 15px;
  width: 15px;
  margin: -1px;
  left: calc(50% - 7px);
  right: auto;
}
.tri-toggle .tri-toggle-false {
  position: absolute;
  display: inline-block;
  height: 100%;
  width: 50%;
  left: 0;
  top: 0;
}
.tri-toggle .tri-toggle-true {
  position: absolute;
  display: inline-block;
  height: 100%;
  width: 50%;
  left: auto;
  right: 0;
  top: 0;
}
.tri-toggle .tri-toggle-wrap.true .tri-toggle-handle {
  left: auto;
  right: 0;
}
.tri-toggle .tri-toggle-wrap.false .tri-toggle-handle {
  left: 0;
  right: auto;
}
.tri-toggle .tri-toggle-wrap.off .tri-toggle-handle {
  left: calc(50% - 7px);
  right: auto;
}
.tri-toggle .tri-toggle-wrap.true {
  background-color: #00c981;
}
.tri-toggle .tri-toggle-wrap.false {
  background-color: #c90000;
}
