.modal.mobile-app-download-notification .modal-dialog-foo {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  margin: 0 auto;
}

.modal.mobile-app-download-notification {
  display: flex !important;
  align-items: center;
}
.modal.mobile-app-download-notification .modal-body {
  text-align: center;
}
.modal.mobile-app-download-notification h3 {
  font-weight: bold;
  font-size: 1.8rem;
}
.modal.mobile-app-download-notification .modal-footer {
  text-align: center;
}
.modal.mobile-app-download-notification form {
  padding: 20px 0 0 0;
  text-align: center;
}
.modal.mobile-app-download-notification form input[type='checkbox'] {
  margin: 0 5px 0 0;
}
