.prefetch-locations-container h3 {
  margin-top: 25px;
}

.prefetch-loader-container {
  text-align: center;
  height: 300px;
  width: 50%;
  padding: 100px 0px;
  color: #ccc;
  font-size: 2em;
  font-family: "Lato", sans-serif;
  font-weight: 300;
}

.prefetch-loader-container .i {
  color: #bbb;
  margin: 0px auto;
  font-size: 1em;
}

/* BEGIN: Media Download */

.prefetch-locations-container section {
  margin-bottom: 45px;
}

.prefetch-locations-container section:last-child {
  margin-bottom: 0;
}

.prefetch-locations-container .prefetch-window {
  margin-bottom: 15px;
  padding: 0 15px;
}

.prefetch-locations-container .prefetch-window .prefetch-window-start {
  margin-bottom: 2px;
}

.prefetch-locations-container .prefetch-window-label {
  display: inline-block;
  width: 90px;
}

.prefetch-locations-container .notes-title {
  font-size: 16px;
  margin-bottom: 5px;
}

.media-download-window-edit.modal .modal-body .modal-title {
  font-size: 2rem;
  margin: 0 0 10px;
}

.media-download-window-edit.modal .modal-body p {
  font-size: 14px;
}

.media-download-window-edit.modal .note-end-time {
  margin-bottom: 30px;
}

.media-download-window-edit .form-label {
  display: inline-block;
  font-weight: 700;
  margin-bottom: 5px;
  width: 90px;
}

.media-download-window-edit legend {
  border-bottom: none;
  float: left;
  font-size: 14px;
  padding-top: 3px;
}

.media-download-window-edit .time-empty {
  display: block;
  margin-left: 90x;
  padding-top: 4px;
}

.media-download-window-edit .time-separator {
  margin: 0 2px;
}

.media-download-window-edit .form-control {
  display: inline-block;
  height: 30px;
  padding: 5px 10px !important;
  vertical-align: middle;
  width: auto;
}

.media-download-window-edit .input-dropdown {
  vertical-align: middle;
}

.media-download-window-edit .input-dropdown ul {
  max-height: 170px;
  overflow-y: scroll;
  width: 60px;
}

.media-download-window-edit .input-dropdown ul > li {
  padding: 3px;
  text-align: center;
}

.media-download-window-edit .input-dropdown ul > li.active {
  background-color: #DFDFDF;
}

.media-download-window-edit .input-dropdown ul > li:hover {
  background-color: #EFEFEF;
}

.media-download-window-edit .input-dropdown input[type="text"].time {
  text-align: center;
  width: 50px;
}

.media-download-window-edit .ampm {
  margin-left: 5px;
}

.media-download-window-edit .next-day {
  display: block;
  margin: 5px 0 0 90px;
}

@media (min-width: 768px) {
  .media-download-window-edit .next-day {
    display: inline;
    margin-left: 5px;
  }
}

.media-download-window-edit.modal .modal-footer button.device-dl-window-clear-btn {
  width: auto;
}

.media-download-window-edit .window-cleared .form-group {
  margin-bottom: 5px;
}

.media-download-window-edit .window-cleared legend,
.media-download-window-edit .window-cleared .time-empty {
  padding-top: 0;
}

.media-download-window-edit .window-cleared legend {
  margin-bottom: 0;
}

.media-download-window-edit .window-cleared .note-end-time {
  margin-bottom: 15px;
}

/* END: Media Download */

.prefetch input[type="radio"] {
  margin: 3px 5px 0 15px;
}

.prefetch .location-default {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

/* Safari was shrinking the radio buttons as the selection text got wider */
.prefetch .location-default input[type="radio"] {
  flex-shrink: 0;
}

.prefetch .location-list {
  margin-top: 8px;
}

.table-list .locations .admin-toolbar .filter {
  margin-left: 0;
}
.table-container.locations-outer-container {
  padding-top: 40px;
}

.prefetch-locations-container .table-container > div {
  overflow-x: auto;
  margin-top: 10px;
}

.prefetch-locations-container .table-container table.table-hover {
  margin-bottom: 0;
  table-layout: fixed;
  width: 40%;
}

.prefetch-locations-container .table-container thead {
  display: block;
  width: 100%;
}

.prefetch-locations-container .table-container tbody {
  display: block;
  width: 100%;
  max-height: calc(100vh - 340px);
  height: 100%;
  min-height: 40px;
  overflow-y: auto;
  background-color: #FFFFFF;
  border: 1px solid #ddd;
  border-radius: 4px;
}

table.table.location-table > thead > tr > th {
  padding-top: 8px;
  border-bottom: none;
}

.prefetch-locations-container td {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

.prefetch-locations-container table td div {
  overflow: hidden;
  text-overflow: ellipsis;
}

.prefetch-locations-container table > tbody > tr:first-child > td {
  border-top: none;
}

.prefetch-locations-container table thead th, .prefetch-locations-container table tbody td {
  padding: 10px 4px;
  white-space: nowrap;
  margin: 0;
  border-top-width: 0;
}

.prefetch-locations-container table > thead > tr:first-child > th {
  padding-left: 0px;
}

.prefetch-locations-container table tbody td {
  padding: 10px 25px;
}

.prefetch-locations-container th.use-prefetch, .prefetch-locations-container td.use-prefetch {
  width: 10%;
  min-width: 50px;
  text-align: center;
}

.prefetch-locations-container th.location-name, .prefetch-locations-container td.location-name {
  width: 38%;
  min-width: 100px;
}

.prefetch-locations-container td.use-prefetch {
  font-size: 1.8rem;
}

.prefetch-locations-container .pagination-controls {
  margin-top: 15px;
}
