.create-mix-station-from-filters .modal-dialog {
  margin: 10px auto;
  max-width: calc(100vw - 20px);
  top: 0;
}

@media (min-width: 768px) {
  .create-mix-station-from-filters .modal-dialog {
      margin-left: auto;
      margin-right: auto;
      max-width: none;
      padding: 0;
      top: 0;
      width: 550px;
  }
}

.create-mix-station-from-filters .modal-title {
  margin-bottom: 15px;
}

.create-mix-station-from-filters .form-container {
  margin-bottom: 20px;
}

.create-mix-station-from-filters h3 {
  font-size: 16px;
  font-weight: 400;
  margin-top: 15px;
}

.create-mix-station-from-filters .modal-body .row {
  margin: 0 -15px;
}

.create-mix-station-from-filters .has-error .control-label,
.create-mix-station-from-filters .validation-error {
  color: #D9534F;
}

.create-mix-station-from-filters .has-error .form-control {
  border-color: #D9534F;
}

.create-mix-station-from-filters .has-error .form-control:focus {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 6px #D9534F;
}

.create-mix-station-from-filters .mix-station-name-form-group {
  margin-bottom: 10px;
}

.create-mix-station-from-filters .modal-content ul li div {
  display: block;
  overflow: visible;
  text-overflow: clip;
  white-space: normal;
}

.create-mix-station-from-filters .modal-body p {
  font-size: 14px;
}

.create-mix-station-from-filters .content-rating-icon {
  margin-top: 10px;
}
.create-mix-station-from-filters .filters-list .filter-category {
  position: relative;
}

.create-mix-station-from-filters .filters-list .filter-category-name {
  display: inline-block;
  position: absolute;
  width: 50px;
}

.create-mix-station-from-filters .filters-list.has-wide-filter-name .filter-category-name {
  width: 110px;
}

.create-mix-station-from-filters .filters-list.has-wide-filter-name .category-filters {
  padding-left: 120px;
}

.create-mix-station-from-filters .modal-body .category-filters {
  margin: 0 0 3px;
  overflow: hidden;
  padding-left: 60px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.create-mix-station-from-filters .filtered-station-list-header {
  color: #777;
  margin-top: -28px;
}

.create-mix-station-from-filters .filtered-station-list-header .tooltip-inner {
  max-width: 280px;
  width: 280px;
}

.create-mix-station-from-filters .filtered-station-list {
  margin-bottom: 0;
  max-height: 270px;
  overflow-x: hidden;
  overflow-y: scroll;
}

@media (min-width: 768px) {
  .create-mix-station-from-filters .filtered-station-list {
    max-height: 320px;
  }
}

.create-mix-station-from-filters .filtered-station-list .filtered-station-item {
  min-height: 40px;
  padding: 5px 0;
  transition: background-color linear 150ms;
}

.create-mix-station-from-filters .filtered-station-list .filtered-station-item:hover {
  background-color: #FAFAFA;
}

.create-mix-station-from-filters .filtered-station-list .filtered-station-item .station-details-column {
  margin-bottom: 0;
}

.create-mix-station-from-filters .rating-header {
  position: absolute;
  left: 47%;
}
.create-mix-station-from-filters .weight-header {
  position: absolute;
  left: 58%;
}

.create-mix-station-from-filters .station-container {
  position: relative;
}

.create-mix-station-from-filters .station-thumbnail {
  background-size: cover;
  height: 40px;
  left: 0;
  position: absolute;
  width: 40px;
}

.create-mix-station-from-filters .station-details {
  padding-left: 50px;
  padding-top: 4px;
}

.create-mix-station-from-filters .modal-content ul li .station-name,
.create-mix-station-from-filters .modal-content ul li .station-desc {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.create-mix-station-from-filters .station-name {
  line-height: 1.1;
  margin-bottom: 2px;
}

.create-mix-station-from-filters .station-desc {
  color: #777;
  font-size: 12px;
  font-weight: 300;
}

.create-mix-station-from-filters .frequency-form-control {
  margin-top: 5px;
}

.create-mix-station-from-filters .remove-station-btn {
  font-size: 18px;
  margin: 0 10px 0 0;
  padding: 4px;
}

.create-mix-station-from-filters .remove-station-btn:hover {
  font-weight: 700;
  text-decoration: none;
}

.create-mix-station-from-filters .activate-form p {
  font-size: 16px;
}

.create-mix-station-from-filters .activate-form .activate-success {
  margin-bottom: 20px;
}

.create-mix-station-from-filters .activate-form .activate-success .fa {
  color: #5CB85C;
}

.create-mix-station-from-filters .activate-form .activate-success .fa.animated {
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
}

.create-mix-station-from-filters .activate-form .activate-actions p {
  margin-bottom: 10px;
}

.create-mix-station-from-filters .activate-form .activate-actions .btn {
  margin-right: 10px;
  width: auto;
}

.create-mix-station-from-filters .activate-actions .play-opts {
  margin-bottom: 10px;
}

.create-mix-station-from-filters .activate-form .activate-actions .btn:last-child {
  margin-right: 0;
}
.create-mix-station-from-filters form .modal-footer .btn-primary span {
  pointer-events: none;
}
