.action-bar {
  background-color: #fafafa;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 8px;
  border-radius: 5px;
  margin-bottom: 20px;
}
.action-bar-title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 61%;
  max-width: 500px;
}
.action-bar-title h2 {
  font-size: 1.4em;
  color: #333;
  margin: auto 0 auto 10px;
  font-weight: 600;
}
.action-bar-title .unsaved {
  margin-left: 10px;
  color: #d9534f;
}
.action-bar-title .saved {
  margin-left: 10px;
  color: #5cb85c;
}
.action-bar-title label {
  width: 100%;
}
.action-bar-title input {
  width: 100%;
  max-width: 225px;
  display: inline-block;
  vertical-align: middle;
}
.action-bar-title a {
  margin-right: 5px;
}
.action-bar-actions {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  margin-left: auto;
  width: 100%;
  max-width: 600px;
}
.action-bar-actions label {
  margin-right: 10px;
}
.action-bar-actions input[type=checkbox] {
  margin-right: 5px;
}
.action-bar-actions a {
  margin-left: 5px;
}

@media screen and (max-width: 768px) {
  .action-bar-title {
    width: 384px;
  }
  .action-bar-title input {
    max-width: 140px;
  }
}

