
.station-info-popup {
  position: fixed;
}

.station-info-popup .modal-dialog {
  width: 90%;
  max-width: 500px;
}

.station-info-popup .station-info {
  padding: 20px;
}

.station-info-popup .modal-dialog .station-info .content-rating-icon {
  display: inline-block;
  left: 0;
  margin-left: 5px;
  position: relative;
  top: 0;
}

.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-g .fg,
.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-pg .fg,
.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-ma .fg {
  color: #FFF;
}

.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-g .bg {
  background-color: #60B570;
}

.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-pg .bg {
  background-color: #439FDC;
}

.station-info-popup .modal-dialog .station-info .content-rating-icon.rating-ma .bg {
  background-color: #D73A3A;
}

.station-info i.play-button {
  font-size: 2.5rem;
  text-transform: uppercase;
  pointer-events: all;
  cursor: pointer;
  color: #777;
  display: inline;
}

.station-info i.play-button span {
  display: inline-block;
  padding-bottom: 5px;
  font-size: 1.5rem;
  vertical-align: middle;
  padding-left: 15px;
}

.station-info i.play-button.disabled {
  display: none;
}

.station-info h2 {
  margin: 0 0 8px 0;
}

.station-info h3 {
  font-size: 1.8rem;
  margin: 0 0 20px 0;
}

.modal .modal-body.station-info p {
  font-size: 1.3rem;
  margin: 0 0 20px 0;
  padding: 0;
}

@media all and (min-width: 521px) {
  .station-info-popup .modal-dialog {
    width: auto;
  }
}

@media all and (min-width: 768px) {
  .station-info i.play-button span {
    line-height: 1.8;
    padding-bottom: 3px;
  }
}
