.content-form.order-device form {
  display: flex;
  flex-direction: column;
  max-width: 968px;
}
.content-form.order-device .form-group.row {
  margin-right: 5px;
}
.content-form.order-device .order-form-address {
  flex: 1 1 auto;
}
.content-form.order-device .username,
.content-form.order-device .zonename,
.content-form.order-device .phone {
  max-width: 300px;
  margin-bottom: 10px;
}
.content-form.order-device .po-box-notice {
  margin-bottom: 15px;
}
.order-cloudbox .po-box-notice {
  margin-bottom: 0;
  margin-top: 15px;
}
.content-form.order-device .address {
  width: 100%;
  max-width: 421px;
  min-width: 150px;
  margin-right: 10px;
}
.content-form.order-device .address2 {
  width: auto;
  max-width: 100px;
  min-width: 80px;
}

.content-form.order-device .city {
  float: left;
  max-width: 250px;
  min-width: 150px;
  margin: 0 1% 15px 0;
  width: 100%;
}

.content-form.order-device .state {
  float: left;
  margin-bottom: 15px;
  margin-right: 1%;
  max-width: 200px;
  min-width: 120px;
  width: 20%;
}

.content-form.order-device .zipcode {
  float: left;
  margin-bottom: 15px;
  max-width: 150px;
  min-width: 100px;
  width: 30%;
}

.content-form.order-device .checkbox {
  margin-bottom: 0;
  margin-right: 40px;
  display: inline-block;
}
.order-device .checkbox input[type='checkbox'],
.order-device input[type='radio'] {
  cursor: pointer;
}
.order-device form input.form-control[disabled], .order-device form select.form-control[disabled] {
  background-color: #e0e0e0;
  border: 1px solid #c1c1c1;
  color: #8c868a;
}
.content-form.order-device form input, .content-form.order-device form select {
  max-width: none;
}
.content-form.order-device .add-billing {
  margin-bottom: 35px;
}
.order-device .order-form-checkout {
  border: 1px solid #e6e6e6;
  background-color: #fafafa;
  border-radius: 4px;
  color: #666;
  height: 100%;
  margin-top: 25px;
  max-width: 330px;
}
.order-device .order-form-checkout .section {
  padding: 23px;
  border-bottom: 1px solid #e6e6e6;
}
.order-device .order-form-checkout .section:last-child {
  border-bottom: none;
}
.order-device .order-form-checkout .section.summary,
.order-device .order-form-checkout .summary .cost-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.order-device .order-form-checkout .section.summary {
  height: 162px;
}

.order-device .order-form-checkout .section.terms  {
  display: flex;
  flex-direction: column;
}
.order-device .order-form-checkout .summary .cost-detail {
  width: 100%
}
.order-device .order-form-checkout .summary .cost-item {
  margin-bottom: 5px;
}
.order-device .order-form-checkout .summary .cost-item:last-child {
  border-top: 1px black solid;
  margin-top: 10px;
  padding-top: 10px;
  font-weight: 600;
}
.order-device .cost-item .description {
  flex: 4;
}
.order-device .cost-item .cost {
  display: flex;
  flex: 1;
  justify-content: space-between;
}
.order-device .shipping-note {
  margin: 5px 0;
  font-size: 12px;
}
.order-device input[type="radio"] + label {
  margin-left: 5px;
  cursor: pointer;
}
.order-form-checkout .shipping-rates .empty {
  height: 25px;
}
.order-form-checkout .shipping-rates .shipping-rate-next-input,
.order-form-checkout .shipping-rates .shipping-rate-next-label {
  cursor: not-allowed;
}
.order-form-checkout .shipping-rates .shipping-rate-next-label {
  color: #999;
}
.order-form-checkout .shipping-rates .help-text {
  display: inline-block;
  font-size: 12px;
  margin-bottom: 15px;
}
.order-device label {
  font-weight: 400;
}
.order-device .checkbox-inline {
  margin-left: 0px;
}
.order-device .prop65, .edit-location .prop65 {
  font-size: 12px;
  padding-bottom: 20px;
}
.order-device .prop65 img {
  float: left;
  padding: 5px;
}

.order-device .warranty-link {
  font-size: 12px;
  padding-top: 20px;
  text-align: center;
}
@media screen and (min-width: 968px) {
  .content-form.order-device form {
    flex-direction: row;
  }
}
