.content-rating-icon {
  display: inline-block;
  height: 15px;
  position: relative;
  vertical-align: middle;
  width: 25px;
}

.content-rating-icon.has-tooltip {
  cursor: default;
}

.content-rating-icon .bg {
  pointer-events: none;
  background: #FFF;
  border-radius: 4px;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.content-rating-icon .fg {
  pointer-events: none;
  align-items: center;
  background: transparent;
  border-radius: 4px;
  border: transparent;
  box-sizing: border-box;
  color: #60B570;
  display: flex;
  font-family: arial;
  font-size: 1rem;
  font-weight: 900;
  height: 100%;
  justify-content: center;
  left: 0;
  letter-spacing: 0.12px;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
}

.content-rating-icon.rating-g .fg   { color: #60B570; }
.content-rating-icon.rating-pg .fg  { color: #439FDC; }
.content-rating-icon.rating-ma .fg  { color: #D73A3A; }

content-rating-icon .tooltip.in {
  opacity: 1 !important;
}

content-rating-icon .tooltip .tooltip-inner {
  filter: alpha(opacity=100);
  max-width: 300px;
  opacity: 1;
  text-align: left;
  width: auto;
}

.content-rating-icon.rating-g.rating-summary + .tooltip .tooltip-inner,
.content-rating-icon.rating-pg.rating-summary + .tooltip .tooltip-inner {
  white-space: nowrap;
}

.content-rating-icon.rating-ma.rating-summary + .tooltip .tooltip-inner {
  min-width: 250px;
}

content-rating-icon .tooltip-arrow {
  background: transparent;
}

.station-list content-rating-icon .tooltip .tooltip-inner {
  background: #EEE;
  color: #000;
}

.station-list content-rating-icon .top .tooltip-arrow {
  border-top-color: #EEE !important;
}

.station-list content-rating-icon .left .tooltip-arrow {
  border-left-color: #EEE !important;
}

.station-list content-rating-icon .bottom .tooltip-arrow {
  border-bottom-color: #EEE !important;
}

.station-list content-rating-icon .right .tooltip-arrow {
  border-right-color: #EEE !important;
}

@media (max-width: 500px) {
  .station-list content-rating-icon .tooltip {
    left: calc( (100vw - 300px) / 2 ) !important;
    margin: -50px auto 0 auto;
    top: unset !important;
  }

  .station-list content-rating-icon .tooltip .tooltip-inner {
    max-width: 300px;
    min-width: 220px;
  }

  .station-list content-rating-icon .top .tooltip-arrow {
    display: none;
  }
}
