
daypart-scheduler * {
  box-sizing: border-box;
}

daypart-multi-slider {
  position: relative;
  display: block;
  height: 35px;
  overflow: hidden; }
  daypart-multi-slider .slot {
    position: absolute;
    display: inline-block;
    top: 5px;
    bottom: 5px;
    border-radius: 3px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    line-height: 25px;
    color: #fff; }
  daypart-multi-slider .ghost {
    min-width: 65px;
    background: #5ab57a;
    opacity: 0.6;
    display: none;
    cursor: pointer; }
  daypart-multi-slider:hover .ghost {
    display: block; }
  daypart-multi-slider.slot-hover .ghost {
    display: none; }
  daypart-multi-slider.dragging .ghost {
    display: none; }
  daypart-multi-slider.selected .ghost {
    display: block; }
  daypart-multi-slider daypart-slot {
    -webkit-transition: background 0.1s ease-in;
    cursor: pointer;
    min-width: 10px !important;
    background: #4eb8d5; }
    daypart-multi-slider daypart-slot .handle {
      position: absolute;
      top: 0;
      bottom: 0; }
      daypart-multi-slider daypart-slot .handle.left, daypart-multi-slider daypart-slot .handle.right {
        width: 4px;
        background: #000;
        cursor: col-resize !important;
        opacity: 0.1; }
        daypart-multi-slider daypart-slot .handle.left:hover, daypart-multi-slider daypart-slot .handle.right:hover {
          opacity: 0.3; }
      daypart-multi-slider daypart-slot .handle.left {
        left: 0; }
      daypart-multi-slider daypart-slot .handle.right {
        right: 0; }
    daypart-multi-slider daypart-slot.active {
      z-index: 10;
      background: #5ab57a;
      color: #fff; }
  daypart-multi-slider.dragging daypart-slot {
    cursor: ew-resize; }
.scheduler {
  border: 1px solid #ccc;
  border-radius: 5px;
  float: left;
  width: 100%;
  background: #fff; }
  .scheduler .labels, .scheduler .data-area {
    display: block;
    float: left; }
  .scheduler .labels {
    width: 90px;
    color: #666;
    border-right: 1px solid #ddd; }
    .scheduler .labels .row {
      padding: 0 10px;
      border-bottom: 1px solid #ddd; }
  .scheduler .data-container {
    overflow-x: auto;
    width: calc(100% - 90px); }
  .scheduler .data-area {
    width: 2880px; }
  .scheduler .row {
    position: relative;
    height: 35px;
    line-height: 35px; }
    .scheduler .row:last-child, .scheduler .row:last-child .grid-container {
      border-bottom: none; }
  .scheduler .timestamps {
    color: #aaa;
    position: relative; }
    .scheduler .timestamps .grid-container div {
      border-right: 1px solid #eee; }
      .scheduler .timestamps .grid-container div:last-child {
        border-right: none; }

.grid-container {
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  border-bottom: 1px solid #ddd; }
  .grid-container div {
    position: absolute;
    bottom: 0;
    top: 0;
    padding: 0 6px; }
  .grid-container.striped div:nth-child(even) {
    background: #fafafa; }
  .grid-container.no-labels .content {
    font-size: 1.3rem;
    display: none; }

.scheduler-container.zoom0 daypart-scheduler .data-area {
  width: 1040px;
}
.scheduler-container.zoom1 daypart-scheduler .data-area {
  width: 2080px;
}
.scheduler-container.zoom2 daypart-scheduler .data-area {
  width: 4160px;
}
.scheduler-container.zoom3 daypart-scheduler .data-area {
  width: 8320px;
}

