message-scheduler * {
  box-sizing: border-box;
}

message-scheduler {
  margin-top: 10px;
}

.message-toolbar-2 .left {
  position: absolute;
  left: 0;
  top: 14px;
  display: inline-block;
  width: 350px;
  height: 34px;
}
.message-toolbar-2 .right {
  position: absolute;
  right: 0;
  top: 14px;
  display: inline-block;
  height: 34px;
}
.message-toolbar-2 .add-button {
  height: 32px;
  background-color: #1283C7;
  border: 1px solid #1283C7;
  margin-right: 9px;
  padding-top: 7px;
  color: white;
  font-size: 1.4rem;
  line-height: 1.5rem;
  vertical-align: initial;
}

.message-toolbar-2 .view-selector-btn {
  display: inline-block;
  background-color: #fff;
  padding: 6px 9px;
  border: 1px solid #e1e1e1;
  color: #484748;
  cursor: pointer;
  font-size: 1.3rem;
  max-width: 33px;
}
.message-toolbar-2 .view-selector-btn i {
  display: inline-block;
  width: 13px;
  height: 13px;
}
.message-toolbar-2 .view-selector-btn.list-view {
  border-radius: 5px 0 0 5px;
  border-right: 0;
  border-right: none;
}
.message-toolbar-2 .view-selector-btn.calendar-view {
  border-radius: 0 5px 5px 0;
  position: relative;
  border-left: none;
  left: -2px;
}
.message-toolbar-2 .toolbtn:hover {
  background-color: #eee;
}
.message-toolbar-2 .toolbtn:active {
  background-color: #d7d7d7;
}
.message-toolbar-2 .view-selector-btn.selected {
  background-color: #1283c7;
  color: #fff;
}
.message-toolbar-2 .zoom-btn,
.message-toolbar-2 .week-selection-btn {
  background-color: #fff;
  border: 1px solid #e1e1e1;
  color: #484748;
  cursor: pointer;
  font-size: 1.3rem;
}
.message-toolbar-2 .zoom-btn {
  padding: 7px 8px 7px 10px;
  width: 32px;
  max-width: 33px;
  max-height: 32px;
  display: inline-block;
}
.message-toolbar-2 .zoom-btn.zoom-out {
  border-radius: 5px 0 0 5px;
  border-right: 0;
  margin-left: 8px;
}
.message-toolbar-2 .zoom-btn.zoom-in {
  border-radius: 0 5px 5px 0;
  margin-right: 10px;
  position: relative;
  left: -3px;
}
.message-toolbar-2 .week-selection-btn.previous {
  border-radius: 5px 0 0 5px;
  padding: 7px 9px;
  margin-left: 10px;
}
.message-toolbar-2 .week-selection-btn.today {
  border-left: none;
  border-right: none;
  padding: 7px 8px 7px 9px;
}
.message-toolbar-2 .week-selection-btn.next {
  border-radius: 0 5px 5px 0;
  margin-right: 10px;
  padding: 7px 6px 7px 11px;
}
.message-toolbar-2 select {
  height: 32px;
}

.message-schedule-edit-screen .main-content .scheduler-container,
.message-schedule-container .main-content .agenda-container {
  margin-left: -15px;
  margin-right: -15px;
}
.message-schedule-container .main-content .agenda-container.show-locations,
.message-schedule-container .main-content .scheduler-container.show-locations {
  width: 100%;
}

.message-schedule-edit-screen .action-bar-actions .btn.save-button {
  margin-bottom: 0;
}

.message-schedule-edit-screen .toolbar-container {
  padding: 0 15px;
  display: inline-block;
  width: 100%;
  min-height: 50px;
}

@media screen and (max-width: 768px) {
  .message-schedule-edit-screen .action-bar-actions .btn.save-button .long-text {
    display: none;
  }
  .message-toolbar-2 .long-text {
    display: none;
  }
}

message-schedule-multi-slider {
  position: relative;
  display: block;
  height: 35px;
  overflow: hidden; }
  message-schedule-multi-slider .slot {
    position: absolute;
    display: inline-block;
    top: 5px;
    bottom: 5px;
    border-radius: 3px;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    line-height: 25px;
    color: #fff; }
  message-schedule-multi-slider .ghost {
    min-width: 75px;
    background: transparent;
    color: black;
    opacity: 0.6;
    display: none;
    cursor: pointer; }
  message-schedule-multi-slider .ghost i.fa {
    color: #5ab57a;
  }
  message-schedule-multi-slider:hover .ghost {
    display: block; }
  message-schedule-multi-slider.slot-hover .ghost {
    display: none; }
  message-schedule-multi-slider.dragging .ghost {
    display: none; }
  message-schedule-multi-slider.selected .ghost {
    display: block; }
  message-schedule-multi-slider message-schedule-slot {
    -webkit-transition: background 0.1s ease-in;
    cursor: pointer;
    min-width: 1px !important;
    background: #4eb8d5; }
    message-schedule-multi-slider message-schedule-slot div {
      height: 100%;
    }
    message-schedule-multi-slider message-schedule-slot.active {
      z-index: 10;
      background: #5ab57a;
      color: #fff; }
  message-schedule-multi-slider.dragging message-schedule-slot {
    cursor: ew-resize; }

.message-agenda-table {
  max-width: 600px;
  width: auto;
}
.message-agenda-divider {
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
  margin-top: 10px;
}
.message-agenda-table td {
  padding: 4px 0;
  font-size: 1.4rem;
  color: #656565;
  font-weight: 300;
}
.message-agenda-table .date {
  width: 150px;
  font-weight: 600;
}
.message-agenda-table .time {
  width: 80px;
  cursor: pointer;
  padding: 0 10px;
}
.message-agenda-table .message {
  width: auto;
  cursor: pointer;
  color: #373737;
  font-weight: 500;
  padding: 6px 10px;
}
.message-agenda-table .message i {
  color: #e0e0e0;
  padding-left: 10px;
}
.message-agenda-table tr:hover .time,
.message-agenda-table tr:hover .message {
  background-color: #f5f5f5;
  border-radius: 2px;
}
.message-agenda-table tr:hover .message i {
  color: #d8d7d7;
}
.message-agenda-table .toolbar a {
  color: #000;
  width: 10%;
  cursor: pointer;
}
.message-agenda-table .toolbar a:hover {
  color: #1283ff;
}

.schedule-title {
  display: inline-block;
}
.message-schedules h2 {
  font-size: 1.4rem;
  font-family: "Lato", sans-serif;
  font-weight: 600;
  color: #666;
  line-height: 18px;
  margin-top: 0;
}
.scheduler-container.zoom0 message-scheduler-calendar .data-area {
  width: 1440px;
}
.scheduler-container.zoom1 message-scheduler-calendar .data-area {
  width: 2880px;
}
.scheduler-container.zoom2 message-scheduler-calendar .data-area {
  width: 5760px;
}
.scheduler-container.zoom3 message-scheduler-calendar .data-area {
  width: 11520px;
}
.btn.add-message-schedule {
  cursor: pointer;
}
.no-scheduled-message {
  margin: 20px 0;
  text-align: center;
}
.no-scheduled-message .fa-microphone-slash {
  font-size: 4.5rem;
  color: #ddd;
}
.no-scheduled-message h2 {
  font-size: 2rem;
}
.no-scheduled-message p {
  font-size: 1.5rem;
  max-width: 275px;
  margin: 0 auto;
  color: #aaa;
}
.no-scheduled-message a {
  font-size: 1.5rem;
  color: #1283c7;
  cursor: pointer;
}
message-scheduler-agenda > div {
  width: 100%;
  border: 1px solid #cdcdcd;
  border-radius: 7px;
  padding: 20px;
}
.message-schedule-edit-screen form .error {
  padding-left: 5px;
  margin: -22px 0 -13px 0;
  color: #d9534f;
}
.modal.message-scheduler .modal-dialog {
  padding: 15px;
}
