.daypart-popup-toolbar {
  position: absolute;
  width: auto;
  transform: translateX(-50%);
  z-index: 1000;
}
.daypart-popup-toolbar .toolbar {
  background-color: #efefef;
  color: #444;
  width: auto;
  padding: 5px;
  border: 1px solid #dedede;
  box-shadow: 1px 1px 1px #ddd;
  border-radius: 5px;
}
.daypart-popup-toolbar .toolbar .button {
  cursor: pointer;
}
.daypart-popup-toolbar .toolbar .button.delete {
  padding-right: 5px;
  border-right: 1px solid #ddd;
  margin-right: 5px;
}
.daypart-popup-toolbar .tail {
  position: relative;
  background: #efefef;
  margin-top: -1px;
}
.daypart-popup-toolbar .tail:after, .daypart-popup-toolbar .tail:before {
  top: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.daypart-popup-toolbar .tail:after {
  border-color: rgba(223, 223, 223, 0);
  border-top-color: #efefef;
  border-width: 8px;
  margin-left: -8px;
}
.daypart-popup-toolbar .tail:before {
  border-color: rgba(222, 222, 222, 0);
  border-top-color: #dedede;
  border-width: 9px;
  margin-left: -9px;
}
