.account-playlists .playlist-toolbar .filters {
  display: none;
}
.mobile .account-playlists .playlist-toolbar .filters {
  display: block;
}

.playlist-ccm-station-filter {
  width: auto;
  margin: 0 auto;
  display: none;
  padding: 0 0 10px 0;
  font-size: 1.6rem;
}
.playlist-ccm-station-filter .filter-main-screen {
  width: 100%;
  position: relative;
  left: 0;
  top: 0;
  background-color: #fcfcfc;
  border-right: 1px solid #f7f7f7;
  z-index: 1500;
}

.mobile .playlist-ccm-station-filter.open .filter-main-screen {
  background-color: #D9EDF7;
}
.playlist-ccm-station-filter .filter-main-screen .filter-category {
  width: 100%;
  text-align: left;
  padding: 0;
  background: transparent;
  border: none;
}
.playlist-ccm-station-filter .filter-main-screen ul {
  width: 100%;
}
.playlist-ccm-station-filter .filter-main-screen ul li {
  vertical-align: middle;
  padding: 8px 0px;
  cursor: pointer;
}
.playlist-ccm-station-filter .filter-main-screen ul li.active {
  background-color: #eceff1;
}
.playlist-ccm-station-filter .filter-main-screen ul li.category {
  border-bottom: 1px solid #ddd;
}
.playlist-ccm-station-filter .filter-main-screen ul li.category.genre {
  border-top: 1px solid #ddd;
}
.playlist-ccm-station-filter .filter-main-screen ul li.category div {
  font-weight: 500;
}
.playlist-ccm-station-filter .filter-main-screen ul li li {
  padding: 1px 15px;
  font-weight: normal;
}
.playlist-ccm-station-filter .filter-main-screen ul li li.expand {
  padding: 4px 0 5px 30px;
  color: #0cace4;
}

.playlist-ccm-station-filter .filter-main-screen ul li li.content-rating-info-btn .fa {
  font-size: 14px;
  line-height: 1.42857143;
  margin: 0 5px 0 0;
  vertical-align: initial;
  width: auto;
}

.playlist-ccm-station-filter .filter-main-screen ul li .sub-category.collapsed {
  display: none;
}

.playlist-ccm-station-filter .filter-main-screen > ul > li.open {
  /*border-bottom: none;*/
}

.playlist-ccm-station-filter .filter-main-screen .all-stations {
  cursor: default;
}
.playlist-ccm-station-filter .filter-main-screen .show-stations-for {
  cursor: default;
  font-size: 1.3rem;
  padding-left: 15px;
  padding-top: 8px;
}
.playlist-ccm-station-filter .filter-main-screen .all-stations .reset-label {
  display: inline-block;
  position: relative;
  text-align: left;
  padding-left: 15px;
}
.playlist-ccm-station-filter .filter-main-screen .reset-button {
  text-align: right;
  font-weight: normal;
  font-size: 1.3rem;
  text-decoration: underline;
  padding: 8px 8px 2px 8px;
  cursor: default;
  border: none;
}
.playlist-ccm-station-filter .filter-main-screen ul li.category div.reset-button {
  font-weight: normal;
}
.playlist-ccm-station-filter .filter-main-screen .reset-button span {
  cursor: pointer;
}
.playlist-ccm-station-filter .filter-main-screen .show-stations-for .reset-button {
  cursor: pointer;
}
.playlist-ccm-station-filter .filter-main-screen .filter-menu,
.filter-main-screen .filter-section button.btn.filter-category,
.filter-main-screen .filter-menu button.filter-category {
  font-size: 14px;
}
.playlist-ccm-station-filter .filter-main-screen .filter-menu button.filter-category,
.playlist-ccm-station-filter .filter-main-screen .filter-menu {
  font-weight: 700;
}
.playlist-ccm-station-filter .filter-main-screen .filter-menu li img,
.playlist-ccm-station-filter .filter-main-screen .filter-menu li i {
  pointer-events: none;
}
.playlist-ccm-station-filter .filter-main-screen .show-all,
.playlist-ccm-station-filter .filter-main-screen .most-popular,
.playlist-ccm-station-filter .filter-main-screen .favorites,
.playlist-ccm-station-filter .filter-main-screen .recent,
.playlist-ccm-station-filter .filter-main-screen .newest-releases {
  padding: 4px 0;
}
.playlist-ccm-station-filter .filter-main-screen .pandora {
  padding: 2px 0 6px 0;
}
.playlist-ccm-station-filter .filter-main-screen .pandora img, .filter-category.pandora img {
  height: 13px;
  margin-left: 1px;
}
.playlist-ccm-station-filter .filter-main-screen .recent {
  margin-bottom: 10px;
}
.playlist-ccm-station-filter .filter-main-screen .separated {
  display: inline-block;
  position: absolute;
  right: 5px;
  top: 8px;
}

.playlist-ccm-station-filter > div {
  width: 80vw;
  margin: 0 auto;
}

.playlist-ccm-station-filter .categorySelectContainer {
  display: none;
}

.playlist-ccm-station-filter .categorySelectContainer.show {
  display: inline-block;
}

.playlist-ccm-station-filter label {
  margin: 12px 0;
  font-size: 1.6rem;
  line-height: 23px;
}

.playlist-ccm-station-filter ul {
  list-style-type: none;
  margin: 0;
  padding: 8px 0 0 0;
}
.playlist-ccm-station-filter i {
  margin-right: 5px;
  margin-left: 15px;
  line-height: 23px;
  vertical-align: middle;
  font-size: 1.4rem;
  width: 20px;
  text-align: center;
}
.playlist-ccm-station-filter .sub-category ul li i {
  margin-left: 7px;
}
.playlist-ccm-station-filter ul.menu-list {
  padding-top: 0;
}
.playlist-ccm-station-filter i.right {
  float: right;
}

.playlist-ccm-station-filter ul.menu-list .tooltip .tooltip-inner {
  text-align: left;
}

.playlist-ccm-station-filter .category.rating.open {
  padding-bottom: 45px;
}

@media screen and (min-width: 640px) {
  .playlist-ccm-station-filter {
    width: 230px;
    padding: 0;
    display: inline-block;
    justify-content: left;
    vertical-align: top;
    margin-right: 10px;
    font-size: 1.4rem;
  }
  .playlist-ccm-station-filter > div {
    width: auto;
    margin: 0;
  }

  .playlist-ccm-station-filter label {
    margin: 0 10px 0 0;
  }

  .playlist-ccm-station-filter .filter-main-screen {
    height: calc(100vh - 170px);
    overflow-y: scroll;
  }
  .mobile .playlist-ccm-station-filter.open .filter-main-screen {
    height: calc(100vh - 95px);
  }
}

.mobile .playlist-ccm-station-filter {
  display: none;
}
.mobile .playlist-ccm-station-filter.open {
  display: block;
}

@media (min-width: 500px) {
  .mobile .playlist-ccm-station-filter.open {
    display: inline-block;
  }
  .mobile .station-list.filterOpen {
    width: calc(100% - 260px);
    display: inline-block;
    vertical-align: top;
    position: relative;
  }
  .mobile .account-playlist.image-list {
    padding-top: 0;
  }
}

@media (min-width: 1200px) {
  .filter-main-screen .filter-menu button.filter-category {
    font-weight: 700;
  }
}

.pandora i {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: url(../images/pandora-black.svg);
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 17px;
  margin-right: 9px;
}

.company-stations .pandora.active i,
.edit-mix-station .pandora.active i {
  filter: invert(1);
}
