
.modal.stream-modal .modal-dialog {
  width: 100%;
  max-width: 600px;
}
@media screen and (max-width: 620px) {
  .modal.stream-modal .modal-dialog {
    max-width: calc(100vw - 20px);
  }
}

.modal.stream-modal .legal-warning {
  margin-bottom: 15px;
}
.modal.stream-modal .legal-warning strong {
  font-family: "Lato-Bold", sans-serif;
}

.modal.stream-modal .location-field,
.modal.stream-modal .zone-name {
  display: inline-block;
  width: 49%;
  vertical-align: top;
}
.modal.stream-modal .zone-name input, .modal.stream-modal .location-select input {
  padding: 6px 12px;
  height: 34px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  width: 100%;
}
.modal.stream-modal .location-select input[disabled] {
  background: #F0F0F0;
  border: 1px solid #dbdbdb;
}
.modal.stream-modal .location-field ol, .modal.stream-modal .location-select input {
  width: calc(100% - 10px);
}
.modal.stream-modal .location-field ol button {
  padding: 6px 12px;
}
.modal.stream-modal .region-field {
  width: 49%;
}
.modal.stream-modal .region-field ol {
  width: calc(100% - 10px);
}
.modal.stream-modal .scheduler-popup form .row label {
  width: 80px;
}
.modal.stream-modal .terms {
  margin-top: 20px;
}
.modal .modal-body .alert p strong {
  font-family: "Lato-Bold", sans-serif;
}
.modal.stream-modal .error {
  color: #d9534f;
}
.modal.stream-modal ul.dropdown-menu {
  max-height: 300px;
  overflow: auto;
}
