.header-toolbar {
  background: #FAFAFA;
  position: fixed;
  padding: 0;
  width: 100%;
  height: 49px;
  top: 0;
  left: 0;
  z-index: 1015;
}
.header-toolbar.trial,
.header-toolbar.sub-paused {
  height: 90px;
}
.header-toolbar .container {
  height: 100%;
}

.main-content-container.mobile.filter-open .header-toolbar {
  display: none;
}
.main-content-container.mobile.filter-open .account-content-container {
  padding-top: 0;
}
.header-toolbar .header-toolbar-content {
  position: relative;
  display: block;
  margin: 0;
}

.header-toolbar .trial-message,
.header-toolbar .sub-paused-message,
.header-toolbar .admin-required,
.header-toolbar .trial-sign-up {
  font-family: "Lato";
  font-weight: 600;
  letter-spacing: 0.25px;
  color: #FFFFFF;
  height: 20px;
  text-align: center;
}

.header-toolbar .trial-banner,
.header-toolbar .sub-paused-banner {
  position: relative;
  background: #393D41;
  margin-bottom: 0;
  height: 45px;
  z-index: 1;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.35);
  font-size: 1.4rem;
}

.header-toolbar .trial-banner .trial-message,
.header-toolbar .sub-paused-banner .sub-paused-message {
  line-height: 45px;
  padding-left: 8px;
}

.header-toolbar .trial-banner .trial-message {
  left: 0;
  position: absolute;
  text-align: left;
}

.header-toolbar .trial-banner .admin-required {
  position: absolute;
  right: 0;
  left: auto;
  padding-right: 10px;
  line-height: 45px;
  text-align: right;
}

.header-toolbar .trial-sign-up {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 140px;
  height: 35px;
  margin: 5px;
  background-color: #00c276;
  font-size: 1.4rem;
  z-index: 2;
  border-radius: 2px;
  padding: 0 0 2px 0;
  border: none;
  text-transform: uppercase;
}

@media all and (max-width: 600px) {
  .sub-paused-banner .sub-paused-message {
    font-size: 1.3rem;
  }
}

@media all and (max-width: 500px) {
  .trial-banner .trial-message,
  .trial-banner .admin-required {
    font-size: 1.1rem;
  }

  .sub-paused-banner .sub-paused-message {
    font-size: 0.9rem;
  }
}

@media all and (max-width: 350px) {
  .trial-banner .trial-message,
  .trial-banner .admin-required {
    font-size: 0.9rem;
  }

  .sub-paused-banner .sub-paused-message {
    font-size: 0.8rem;
  }
}

.account-content-container {
  box-sizing: border-box;
  padding-top: 50px;
  min-height: 325px;
  position: relative;
  z-index: 10;
  background-color: #fff;
}
.account-content-container.trial,
.account-content-container.sub-paused {
  padding-top: 91px;
}
.account-content-container .account-content-shadow {
  position: relative;
  z-index: 1;
  width: 100%;
}

.account-content-container .account-content-view .row {
  margin-left: auto;
  margin-right: auto;
}

.account-content-container .account-content-view > div > .row:not(.admin-content) > .col-sm-12 {
  padding-left: 0;
  padding-right: 0;
}

.account-content-container .account-content-view .admin-content > .row > div {
  padding-left: 0;
  padding-right: 0;
}

.header-toolbar-nav {
  position: relative;
  padding: 0;
  color: #333;
  display: block;
}
.header-toolbar-nav .logo {
  background: url("../images/logo.svg") no-repeat;
  width: 172px;
  height: 35px;
  -moz-background-size: 150px 35px;
  -o-background-size: 150px 35px;
  -webkit-background-size: 150px 35px;
  background-size: 150px 35px;
  top: 0;
  left: 0;
  float: left;
  max-width: 150px;
  margin: 5px 20px 5px 10px;
}
.header-toolbar-nav .gravatar {
  position: absolute;
  left: 180px;
  top: 5px;
  width: 40px;
  height: 40px;
  float: left;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
}
.header-toolbar-nav .header-toolbar-icons {
  display: block;
  width: 100vw;
  clear: both;
  border-top: 1px solid #E5E5E5;
  border-bottom: 1px solid #E5E5E5;
  padding: 5px;
}
.header-toolbar-nav .header-toolbar-icons .icon-container {
  display: flex;
  margin: 0 auto;
  max-width: 450px;
  min-width: 300px;
}
.header-toolbar-nav .header-toolbar-icons .icon-container > div {
  display: flex;
  justify-content: space-around;
}
.header-toolbar-nav .header-toolbar-icons .icon-container .left-bar {
  width: 60%;
}
.header-toolbar-nav .header-toolbar-icons .icon-container .left-bar a.selected {
  color: #0cace4;
}
.header-toolbar-nav .header-toolbar-icons .icon-container .right-bar {
  width: 40%;
}
.header-toolbar-nav .header-toolbar-icons .icon-container .right-bar .impersonation-label {
  font-size: 14px;
  margin-right: 15px;
}

.header-toolbar-nav .header-toolbar-icons a {
  color: #656565;
  display: block;
  padding: 4px 10px;
}
.header-toolbar-nav .header-toolbar-icons a i {
  font-size: 22px;
  width: 20px;
  height: 20px;
  padding: 0px;
  margin: 0px;
}
.header-toolbar-nav .header-toolbar-icons a span.text {
  display: none;
}
.header-toolbar-nav .header-toolbar-icons a:hover {
  text-decoration: none;
  color: #0cace4;
}
.header-toolbar-nav .header-toolbar-icons a:last-child {
  border-right: 0px;
}
.header-toolbar-nav .header-toolbar-icons .right-bar .account-menu {
  padding: 0;
  display: none;
  width: auto;
}
.header-toolbar-nav .account-menu ul {
  position: relative;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.header-toolbar-nav .account-menu > ul > li {
  padding: 7px;
  border-radius: 3px;
  min-width: 100px;
}
.header-toolbar-nav .account-menu > ul {
}
.header-toolbar-nav .account-menu > ul ul {
  display: block;
  position: absolute;
  z-index: 20;
  top: 32px;
  left: 0;
  background-color: #fafafa;
  border: 1px solid #dfdfdf;
  border-radius: 2px 2px 5px 5px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  width: 140px;
  min-width: 140px;
}
.header-toolbar-nav .account-menu > ul ul.collapse {
  display: none;
}
.header-toolbar-nav .account-menu > ul ul > li {
  display: block;
  height: 30px;
  padding: 5px 10px;
}
.header-toolbar-nav .account-menu > ul ul > li a {
  width: 100%;
  padding: 0;
}
.header-toolbar-nav .small-device.username {
  display: inline-block;
  position: absolute;
  margin: 12px;
  right: 50px;
  cursor: default;
}
.header-toolbar-nav .small-device.account-menu {
  display: inline-block;
  float: right;
  margin-right: 10px;
  text-align: right;
  cursor: pointer;
}

.header-toolbar-nav .small-device.account-menu .menu-icon {
  font-size: 2.5rem;
  padding: 6px 10px;
  display: inline-block;
  line-height: 2rem;
  margin-top: 5px;
  pointer-events: none;
}
.header-toolbar-nav .small-device.account-menu > ul {
  display: block;
  left: auto;
  right: 0;
  background: #fafafa;
  border: 1px solid #ccc;
}
.header-toolbar-nav .small-device.account-menu.collapse > ul {
  display: none !important;
}
.header-toolbar-nav .small-device.account-menu > ul > li {
  text-align: left;
  font-size: 1.4rem;
  cursor: pointer;
  padding: 0;
}
.header-toolbar-nav .small-device.account-menu > ul > li:hover {
  background: #EAEAEA;
}
.header-toolbar-nav .small-device.account-menu > ul > li a {
  display: block;
  padding: 7px;
  color: #333;
  width: 100%;
  height: 100%;
}
.header-toolbar-nav .small-device.account-menu > ul > li a.upgrade {
  color: #5CB85C;
}
.header-toolbar-nav .small-device.account-menu > ul > li a i {
  margin-right: 10px;
}
.header-toolbar-nav .header-toolbar-icons.large-device {
  display: none;
}
.header-toolbar-nav .large-device.account-menu > ul > li.touch,
.header-toolbar-nav .large-device.account-menu > ul > li:hover {
  background-color: #656565;
  color: white;
}
.header-toolbar-nav .large-device.account-menu > ul > li:hover > ul {
  display: block;
}
.header-toolbar-nav .small-device.account-menu > ul > li.download-app a {
  color: white;
}
.header-toolbar-nav .account-menu ul li.download-app {
  background: #00C276;
  border: 1px solid #00AB68;
  border-radius: 5px;
}
.header-toolbar-nav .account-menu ul.mobile {
  width: 165px;
  left: -20px;
}
.header-toolbar-nav .account-menu ul li.download-app a {
  color: white;
}
.header-toolbar-nav .header-toolbar-icons .right-bar .large-device.account-menu {
  display: inline-block;
}

.header-toolbar-current-play-infobox {
  padding-bottom: 5px;
  margin: 0 auto;
  text-align: center;
  clear: both;
  width: 100%;
  height: 40px;
  line-height: 36px;
}

.header-toolbar-current-play-infobox .line {
  padding-top: 8px;
  clear: both;
}

.header-toolbar-current-play-infobox .line label {
  letter-spacing: 0.37px;
  text-transform: uppercase;
  color: #333;
  font-size: 12px;
}

.header-toolbar-current-play-infobox .info {
  letter-spacing: 0.35px;
  color: #333;
  font-size: 14px;
  width: 175px;
}

@media screen and (min-width: 750px) {
  .header-toolbar {
    border-bottom: 1px solid #ddd;
  }
  .header-toolbar-nav {
    display: block;
    justify-content: space-between;
  }
  .header-toolbar-nav .header-toolbar-icons.large-device {
    display: inline-block;
    width: calc(100% - 180px);
  }
  .header-toolbar-nav .header-toolbar-icons {
    width: auto;
    max-width: none;
    clear: none;
    padding: 7px 0;
    border: 0;
  }
  .header-toolbar-nav .header-toolbar-icons .icon-container {
    display: flex;
    justify-content: space-between;
    max-width: none;
    margin-right: 15px;
  }
  .header-toolbar-nav .header-toolbar-icons .left-bar a {
    width: auto;
    padding: 0 8px;
    font-size: 1.1rem;
    line-height: 34px;
  }
  .header-toolbar-nav .header-toolbar-icons .left-bar a i {
    display: none;
  }
  .header-toolbar-nav .header-toolbar-icons a span.text {
    display: inline;
    text-transform: uppercase;
  }
  .header-toolbar-nav .small-device.account-menu, .header-toolbar-nav .small-device.username {
    display: none;
  }
  .header-toolbar-nav .small-device.username {
    display: none;
  }
  .header-toolbar-nav .header-toolbar-icons .icon-container > div {
    justify-content: normal;
  }
  .header-toolbar-nav .header-toolbar-icons .icon-container .left-bar {
    width: auto;
  }
  .header-toolbar-nav .header-toolbar-icons .icon-container .right-bar {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .header-toolbar-nav .header-toolbar-icons .icon-container .right-bar .upgrade {
    margin-right: 15px;
  }
  .header-toolbar-nav .header-toolbar-icons .upgrade a {
    width: 100%;
    color: #5CB85C;
    border-bottom: 2px solid rgba(92,184,92,0.7);
    padding: 0;
  }
}
