.artist-radio .account-playlist .no-results-found {
  padding-left: 12px;
  font-size: 1.6rem;
  color: #922;
}
  
.artist-radio .account-playlist .results-loading {
  margin: auto;
  font-size: 50px;
}

.artist-radio .suggested-artists.account-playlist.image-list {
  margin: 50px auto 50px auto;
  max-width: 640px;
}

.artist-radio .account-playlist.image-list .description {
  margin: 50px auto 20px auto;
  width: 100%;
  font-size: 20px;
}

.artist-radio .account-playlist.image-list .station-container:hover .cell {
  top: 0px;
  opacity: 1;
}

.artist-radio .search-input {
  width: 100%;
}

.artist-radio .search-input input.searchbox {
  margin-left: 10px;
  display: inline-block;
  width: 400px;
}

.artist-radio .search-input .btn {
  margin-top: -4px;
}

.artist-radio .account-playlist.image-list .now-playing .btn.skip {
  position: relative;
  width: 113px;
  height: 34px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  background: rgba(216,216,216,0.10);
  color: #ffffff;
  font-size: 1.1rem;
  letter-spacing: 0.34px;
  text-transform: uppercase;
  margin: 0 32px;
  padding: 9px 0;
  pointer-events: all;
  cursor: pointer;
}

.artist-radio .account-playlist.image-list .now-playing .btn.skip {
  margin-top: 51px;
  margin-bottom: 4px;
}

.artist-radio .header {
  display: block;
  text-align: left;
  margin: 12px;
  font-size: 1.8rem;
  color: #191919;
  letter-spacing: -0.36px;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 500;
}

.artist-radio .error-message {
  text-align: center;
  font-size: 16px;
  margin: 50px 0px;
}

@media (max-width: 640px) {
  .artist-radio .suggested-artists.account-playlist.image-list {
    margin: 10px auto 10px auto;
  }
  
  .artist-radio .account-playlist.image-list .description {
    margin: 0px 10px 10px 10px;
    font-size: 16px;
  }

  .artist-radio .station-container:nth-child(3) {
    display: none;
  }

  .artist-radio .search-input input.searchbox {
    width: 75%;
  }
}