.table-list .devices .admin-toolbar .filter {
  margin-left: 0;
}
.table-container.devices-outer-container {
  padding-top: 40px;
}

.devices-container .table-container > div {
  overflow-x: auto;
  margin-top: 10px;
}

.devices-container .table-container table.table-hover {
  margin-bottom: 0;
  table-layout: fixed;
}

.api-keys-container .table {
  margin-bottom: 0;
}

.devices-container .table-container thead {
  display: block;
  width: 100%;
}

.devices-container .table-container tbody {
  display: block;
  width: 100%;
  max-height: calc(100vh - 340px);
  height: 100%;
  min-height: 40px;
  overflow-y: auto;
  background-color: #FFFFFF;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.mobile .devices-container .table-container tbody {
  max-height: calc(100vh - 300px);
}

table.table.device-table > thead > tr > th {
  padding-top: 8px;
  border-bottom: none;
}

.devices-container td {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.devices-container table td div {
  overflow: hidden;
  text-overflow: ellipsis;
}
.devices-container table > tbody > tr:first-child > td {
  border-top: none;
}
.devices-container table th, .devices-container table td {
  padding: 80px;
  white-space: nowrap;
  margin: 0;
  border-top-width: 0;
}
.devices-container th.device-name, .devices-container td.device-name {
  width: 15%;
  min-width: 160px;
  overflow: hidden;
}
.devices-container th.deviceid, .devices-container td.deviceid {
  width: 300px;
  min-width: 300px;
  overflow: hidden;
}
.devices-container th.username, .devices-container td.location {
  width: 50%;
  min-width: 100px;
}
.devices-container th.streamname, .devices-container td.streamname {
  width: 15%;
  min-width: 100px;
}
.devices-container .table-container th.action {
  text-align: center;
  width: 75px;
  min-width: 75px;
  max-width: 75px;
}
.devices-container .table-container td.actions {
  position: relative;
  text-align: center;
  width: 60px;
  min-width: 60px;
  max-width: 60px;
}


table.device-table tbody td > div {
  position: relative;
  min-height: 30px;
  display: flex;
  align-items: center;
}

table.device-table tbody td > div.edit-actions {
  position: absolute;
  top: 10px;
  right: 10px;
}
table.device-table tbody td > div.edit-actions .action-button span {
  display: none;
}
.edit-actions.reveal,
.edit-actions.edit-mode {
  display: inline-block;
}

.devices.empty-device-list p {
  padding: 75px 0 50px 0;
  text-align: center;
  font-size: 2.5rem;
}

.devices-container .pagination-controls,
.api-keys-container .pagination-controls {
  display: flex;
  justify-content: flex-end;
  padding-right: 50px;
}

.api-keys-container td.checkboxen {
  width: 40px;
}

.mobile .account-content-container .account-content-view .devices-container h3 {
  margin-top: 0;
}
.mobile .devices-container .table-container tbody {
  max-height: calc(100vh - 200px);
}

.mobile .api-keys-container .btn-revoke-api-key {
  position: absolute;
  left: 70px;
  top: 9px;
}

@media screen and (min-width: 510px) {
  .table-list .devices .admin-toolbar.left-right {
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    height: 50px;
    margin-bottom: 0;
  }
  .table-list .devices .streamed-container .admin-toolbar .left-side,
  .table-list .devices .admin-toolbar .left-side {
    margin-top: 0;
    min-width: 225px;
  }
  .table-list .devices .admin-toolbar .right-side {
    justify-content: flex-end;
    margin-left: 0;
  }
  .table-list .devices .admin-toolbar .filter {
    width: 200px;
    margin-right: 0;
  }
  .table-list .devices .admin-toolbar.left-right .mass-nav {
    margin-top: 0;
  }
}
@media screen and (max-width: 767px) and (min-width: 510px) {
  .table-list .devices .admin-toolbar .left-side {
    margin-left: 50px;
  }
}

@media screen and (max-width: 510px) {
  .mobile .account-content-container .account-content-view .devices-container h3,
  .account-content-container .account-content-view .devices-container h3 {
    margin-top: 50px;
  }
  .table-list .devices .admin-toolbar .filter {
    width: 100%;
    max-width: none;
  }
  .devices-container .table-container tbody {
    max-height: calc(100vh - 400px);
  }
  .mobile .devices-container .table-container tbody {
    max-height: calc(100vh - 300px);
  }
  .devices-container .admin-toolbar .left-side.searchHidden {
    margin-left: 50px;
    margin-top: 0;
  }
  .account-content-container .account-content-view .devices-container h3.searchHidden {
    margin-top: 20px;
  }
}

@media only screen and (max-width: 1000px) {
  table.device-table {
    max-width: 600px;
    margin: 0 auto;
  }
  table.device-table, .device-table thead, .device-table tbody, .device-table th, .device-table td, .device-table tr {
    display: block;
  }

  .device-table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  .device-table tbody tr {
    border-bottom: 1px solid #ddd;
  }
  .device-table tr:last-child {
    border-bottom: none;
  }

  .device-table tr {
    margin: 0;
    padding-top: 10px;
  }

  .mobile .device-table tr:nth-child(odd) {
    background: #eee;
  }

  .devices-container .device-table td {
    /* Behave  like a "row" */
    display: flex;
    width: 100%;
    padding: 0px 5px 0px 100px;
    border: none;
    border-color: transparent;
    position: relative;
  }

  .device-table td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 5px;
    left: 5px;
    width: 100px;
    padding-right: 20px;
    white-space: nowrap;
  }

  .account-content-container .account-content-view .container.devices-container {
    padding: 10px 20px;
  }

  .devices-container .table-container td.actions {
    min-height: 50px;
    width: 100%;
    min-width: 100%;
    text-align: left;
    font-size: 1.3rem;
  }

  .devices-container table td div {
    overflow: auto;
  }

  table.device-table tbody td > div.edit-actions {
    top: auto;
    right: auto;
  }
  table.device-table tbody td > div.edit-actions .action-button {
    width: 80px;
  }
  table.device-table tbody td > div.edit-actions .action-button span {
    display: inline;
    padding-left: 5px;
  }
  table.device-table td:before { content: attr(data-title) ": "; }
  table.device-table td.actions:before { content: ""; }

}
