.btn-link-muted {
  color: #777;
}

.btn-link-muted:hover,
.btn-link-muted:focus {
  color: #515151;
}

.btn-link-danger {
  color: #d9534f;
}

.btn-link-danger:hover,
.btn-link-danger:focus {
  color: #c9302c;
}

.action-button {
  width: 30px;
  height: 30px;
  padding: 5px 8px;
  font-size: 1.4rem;
  font-weight: 400;
  display: inline-block;
  line-height: 1.8rem;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border-radius: 4px;
  text-decoration: none;
  outline: none;
  box-sizing: border-box;
}
.action-button.btn-default:hover {
  background-color: #e6e6e6;
  border: 1px solid #e1e1e1;
}
.action-button.btn-default {
  background-color: #fff;
  border: 1px solid #e1e1e1;
  color: #484748;
}
.ccm-large-button {
  font-size: 1.4rem;
  line-height: 2rem;
  height: 30px;
  padding: 4px 8px;
}
