.table-list .streams {
  max-width: calc(100% - 5px);
}
.streams .table-container {
  position: relative;
  height: 100%;
}
.streams table {
  border-collapse: collapse;
  margin-bottom: 0;
  margin-top: 10px;
}
.streams .table-container thead {
  display: block;
  width: 100%;
}
.streams .table-container tbody {
  display: block;
  width: calc(100% + 5px);
  max-height: calc(100vh - 380px);
  min-height: 50px;
  overflow-y: auto;
  overflow-x: hidden;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.mobile .streams .table-container tbody {
  max-height: calc(100vh - 300px);
}
.streams .table-container > div {
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  margin-top: 28px;
}
.streams table > thead > tr > th, .streams table > tbody > tr > td {
  white-space: nowrap;
  margin: 0;
  padding: 8px;
}
.streams table > thead > tr > th:last-child, .streams table > tbody > tr > td:last-child {
  padding-right: 20px;
}
.streams table > thead > tr > th {
  border-bottom: none;
}
.streams table td {
  border: 1px solid #ddd;
}
.streams table tr.filter-results > td {
  border: none;
}
.streams table td div, .streams td.song .song-title, .streams td.song .song-artist {
  text-overflow: ellipsis;
  overflow: hidden;
}
.streams .table-container th.action {
  cursor: default;
}
.streams table > tbody > tr.active:first-child > td, .streams table >tbody > tr:first-child >td {
  border-top: none;
}
.streams table tr td:first-child {
  border-left: none;
}
.streams table tr.active td:first-child, .streams table tr.active:hover td:first-child {
  box-shadow: 4px 0 0 #5CB85C inset;
}
.streams table > tbody > tr.active > td {
  background-color: rgba(92,184,92,0.07);
}
.streams .table-container thead .header-cell {
  color: #9E9E9E;
  font-weight: normal;
}
.streams .table-container thead div.order-by {
  color: #424242;
  font-weight: bold;
}
.streams th.selection, .streams td.selection {
  width: 40px;
  min-width: 40px;
  max-width: 40px;
  text-align: center;
  font-size: 1.8rem;
}
.streams th.selection .header-cell {
  margin-left: 9px;
}
.streams td.selection {
  padding: 0;
  height: 1px;
}
.streams td.selection div {
  height: 100%;
  width: 100%;
  margin-left: -4px;
}
.streams th.selection .fa {
  margin-left: -7px;
}
.streams th.selection .fa-check-circle, .streams td.selection .fa-check-circle {
  color: #5CB85C;
}
.streams th.selection .fa-circle-thin, .streams td.selection .fa-circle-thin {
  color: #E4E4E4;
}
.streams th.location-name, .streams td.location-name {
  width: 150px;
  min-width: 150px;
  max-width: 150px;
}
.streams th.streamname, .streams td.streamname {
  width: 200px;
  min-width: 200px;
  max-width: 200px;
}
.streams td.streamname .streamname-edit,
.streams td.streamname .streamname-edit-actions,
.streams td.streamname .streamname-name .reveal a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.streams table tr:hover .streamname-edit-name {
  max-width: 190px;
}
.streams td.streamname .streamname-name input {
  font-weight: 300;
  width: 100%;
}
.streams td.streamname .streamname-name a {
  display: none;
  margin-left: 3px;
}
.streams table tr:hover .streamname-name a {
  display: block;
}
.streams .streamname-edit-actions .actions {
  display: flex;
}
.streams .streamname-edit-actions .edit-field {
  width: 69%;
}
.streams th.status, .streams td.status {
  width: 60px;
  min-width: 60px;
  max-width: 60px;
  text-align: center;
}
.streams td.status .status i {
  color: #5cb85c;
}
.streams th.song, .streams td.song,
.streams th.music, .streams td.music,
.streams th.message-preset, .streams td.message-preset,
.streams th.message-schedule, .streams td.message-schedule {
  width: 150px;
  min-width: 150px;
  max-width: 150px;
}
.streams td.song .song-title {
  font-weight: 500;
}
.streams td.song .song-artist {
  font-size: 11px;
}
.streams th.device, .streams td.device {
  width: 135px;
  min-width: 135px;
  max-width: 135px;
}
.streams th.cloudbox-info, .streams td.cloudbox-info {
  width: auto;
  min-width: 135px;
  max-width: 135px;
}
.streams td.cloudbox-info {
  font-size: 1rem;
  text-overflow: inherit;
  word-wrap: break-word;
}
.streams th.region, .streams td.region,
.streams th.autologin, .streams td.autologin {
  width: 85px;
  min-width: 85px;
  max-width: 85px;
}
.streams th.prefetch,
.streams td.prefetch {
  max-width: 85px;
  min-width: 85px;
  text-align: center;
  width: 85px;
}
.streams th.ad-network,
.streams td.ad-network {
  max-width: 85px;
  min-width: 85px;
  text-align: center;
  width: 85px;
}

.streams th.audio-link,
.streams td.audio-link {
  max-width: 110px;
  min-width: 110px;
  text-align: center;
  width: 110px;
}

.device-dl-media-confirm.modal .modal-dialog {
  max-width: 450px;
}
.device-dl-media-confirm.modal .modal-footer .btn {
  width: auto;
}
.streams th.control, .streams td.control {
  width: 311px;
  min-width: 311px;
  max-width: 311px;
}
.streams td.control {
  text-align: left;
}
.streams th.remove, .streams td.remove {
  width: 65px;
  min-width: 65px;
  max-width: 65px;
}
.streams td.remove, .streams td.autologin {
  text-align: center;
}
.streams {
}
.streams td .btn.disabled {
  color: #dfdfdf;
}

/* Table Layout for Standard Plans */
.streams .table-container.standard > div {
  overflow-x: auto;
}
.streams .table-container.standard tbody {
  width: 100%;
}
.streams th.autologin.standard .header-cell {
  margin-left: -5px;
}
.streams th.location-name.standard, .streams td.location-name.standard {
  width: 33%;
}
.streams th.streamname.standard, .streams td.streamname.standard {
  width: 33%;
}
.streams th.device.standard, .streams td.device.standard {
  width: 33%;
}
.streams .btn.add-zone.standard {
  opacity: 0.35;
}
.streams .pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 0;
}
.streams .pagination-controls dir-pagination-controls {
  position: relative;
  margin-right: 50px;
}
.streams .pagination-controls .selectedCount {
  margin-top: 5px;
}

@media screen and (min-width: 768px) {
  .streams table {
    margin-top: 0;
  }
  .streams .table-container > div {
    margin-top: 5px;
  }
  .streams .table-container tbody {
    max-height: calc(100vh - 340px);
  }
  .table-list .streams {
    max-width: none;
  }
  .streams .pagination-controls {
    flex-direction: row;
  }
  .streams .pagination-controls .selectedCount {
    margin-top: 0;
  }
}
@media screen and (min-width: 970px) {
  .streams .table-container tbody {
    max-height: calc(100vh - 300px);
    height: 100%;
    min-height: 40px;
  }
}

/* 2/7/18 RK: For some reason the opacity of the default bootstrap .btn.disabled causes strange rendering
when scrolling so we need to use border color instead */
.streams .btn.btn-default.action-button.disabled {
  opacity: 1;
  border-color: #dfdfdf;
}

.stream-audio-link-modal p.help-block.validation-error {
  color: #D9534F;
  font-size: 14px;
}
