/* Category filters */
.company-stations .playlist-category-heading {
  font-size: 16px;
  margin: 0 0 8px;
  padding-left: 12px;
}

.station-active-category-filters {
  margin: 0 0 5px 15px;
}

.station-active-category-filters .fa {
  color: #777;
  margin-top: 4px;
  vertical-align: top;
}

.station-active-category-filters .btn {
  font-weight: 400;
  line-height: 1;
  margin: 0 5px 8px 0;
  padding: 0;
  -webkit-transition: background-color ease 150ms;
          transition: background-color ease 150ms;
}

.station-active-category-filters .btn.ng-animate {
  -webkit-transition: none;
          transition: none;
}

.station-active-category-filters .btn:last-child {
  margin-right: 0;
}

.station-active-category-filters .btn:hover {
  text-decoration: none;
}

.station-active-category-filters .btn:hover .label,
.station-active-category-filters .btn:focus .label {
  background-color: #D9534F;
}

.station-active-category-filters .label {
  display: inline-block;
  font-weight: 400;
  padding: .4em .8em .5em;
  position: static;
  top: 0;
}

.station-active-category-filters .label .cat-name {
  display: inline-block;
  margin-right: 5px;
}

.locations-toolbar .cs-yes label, .locations-toolbar .cs-no label {
  cursor: pointer;
  font-weight: normal;
}
.locations-toolbar .cs-yes span, .locations-toolbar .cs-no span {
  padding-left: 3px;
}

@media (max-width: 500px) {
  .company-stations form .toolbar {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
  }
  .company-stations form .toolbar > div {
    width: 100%;
    text-align: center;
  }
}
@media (min-width: 700px) {
  .company-stations .edit-company-stations-header h1 {
    margin: 3px 0 0 0;
  }
  .company-stations .locations-toolbar.location-default div {
    display: inline-block;
    margin-right: 10px;
  }
}

@media (min-width: 768px) and (max-width: 910px) {
  .company-stations .locations-toolbar.location-default div {
    display: block;
    margin-right: 10px;
  }
}

@media (min-width: 800px) {
  .company-stations .locations-container .table-container .table-header .row .location-name {
    width: 160px;
  }
  .company-stations .locations-container .table-container .table-header .row .address {
