  .company-stations .locations-container .table-container .table-body .row .location-name {
    width: 160px;
  }
  .company-stations .locations-container .table-container .table-body .row .address {
    width: calc(100% - 222px);
  }
}

@media (min-width: 992px) {

  .company-stations .source-playlist-column {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    min-width: 400px;
    padding: 0 15px;
    margin-bottom: 15px;
  }
  .company-stations .company-station-column {
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 30px);
    padding: 0 15px;
    margin-bottom: 15px;
  }

  .company-stations .company-station-details {
    margin-top: 0;
  }
  .company-stations .companystations {
    height: 590px;
    margin-bottom: 20px;
  }
  .company-stations .companystations .content {
    overflow-y: auto;
    height: 555px;
  }
  .company-stations .source-playlists .stations-scrollable {
    width: 100%;
    height: 100%;
    min-height: 611px;
  }
  .company-stations .source-playlists.stations .list-results {
    max-height: 490px;
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: stretch;
    float: right;
  }
  .company-stations .locations-container .locations-toolbar .filter {
    max-width: 300px;
  }
  .company-stations .source-playlists.stations .list-results .list-group {
    padding: 0;
    height: 100%;
    max-height: 441px;
  }
  .company-stations .source-playlists.stations .list-results .list-group-item {
    margin-left: 5px;
    width: calc(100% - 10px);
  }
}
@media (min-width: 1200px) {
  .company-stations .source-playlist-column {
    width: calc(100% - 400px);
    min-width: 500px;
  }
  .company-stations .company-station-column {
    width: 390px;
  }
  .company-stations .source-playlists .generic-public-station-filter {
    height: 606px;
  }
  .company-stations .source-playlists.stations .stations-scrollable {
    display: table;
    min-height: 100%;
  }
  .company-stations .source-playlists.stations .list-results {
    display: table-cell;
    height: 100%;
    width: calc(100% - 215px);
  }
  .company-stations .source-playlists.stations .list-results .list-group {
    height: 100%;
    max-height: 480px;
  }
}
