.message-library .admin-toolbar {
  margin-bottom: 15px;
}
.message-library .admin-toolbar .upload-btn {
  margin-right: 0;
}
.message-library .admin-toolbar .btn.request-btn {
  margin-right: 10px;
}
.message-library .admin-toolbar .btn i {
  margin-right: 3px;
}
.message-library .admin-toolbar .filter {
  width: 250px;
  display: inline;
  vertical-align: bottom;
  margin-left: 8px;
}
.message-library table {
  width: 100%;
}

.message-library thead tr {
  background: #f5f5f5;
}

.message-library th, .message-library td {
  font-size: 1.3rem;
  color: #8e8989;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #EFEFEF;
  font-weight: 300;
  padding: 10px 4px;
}
.message-library td {
  line-height: 1.6rem;
  padding: 12px 4px;
}

.message-library tr.list-view-row:hover {
  background-color: #fafafa;
}

.message-library th {
  color: #333333;
  white-space: nowrap;
  font-weight: 400;
  font-size: 13px;
}

.message-library .play-control {
  width: 40px;
  min-width: 40px;
  cursor: pointer;
  text-align: center;
}

.message-library .play-control i.pause-icon {
  color: #333;
}
.message-library .play-control i.play-icon {
  display: block;
  font-size: 1.4rem;
  color: #333;
}

.message-library tr:hover .play-control i.play-icon {
  display: block;
}


.message-library .message_name {
  cursor: pointer;
  width: 55%;
  min-width: 150px;
  font-weight: 400;
  color: #333333;
}

.message-library .message_name .title-edit-input {
  font-size: 1.4rem;
  font-weight: normal;
  letter-spacing: 0.5px;
  line-height: 20px;
  width: 100%;
}
.message-library .created_at {
  cursor: pointer;
  width: 200px;
  min-width: 150px;
}

.message-library .message_file {
  cursor: pointer;
  width: 50px;
  min-width: 50px;
}
.message-library .actions {
  width: 90px;
  text-align: center;
}

.message-library .actions i {
  padding-right: 10px;
}

.message-library .edit-message {
  margin-right: 5px;
}

.message-library .actions {
  white-space: nowrap;
}

.message-library .actions a.edit-message:hover {
  color: #0CACE4;
}

.message-library .actions a.delete-message:hover {
  color: #D9534F;
}

.message-library .actions a.btn.action-button.noop {
  cursor: not-allowed;
  filter: alpha(opacity=65);
  box-shadow: none;
  opacity: .65;
}

.message-library .no-results-message {
  padding: 5px 20px;
}

@media screen and (max-width: 900px) {
  .message-library .help-button .help-label {
    display: none;
  }
  .message-library .upload-btn .upload-label {
    display: none;
  }
}

@media screen and (max-width: 580px) {
  .message-library .admin-toolbar .filter.search-field {
    width: 150px;
  }
}

@media screen and (max-width: 500px) {
  .message-library .title {
    font-size: 1.7rem;
    padding-left: 10px;
    width: 90% !important;
  }
  .message-library .play-control.mobile {
    width: 10%;
  }
}
