.default-container .billing-admin .default-block {
  margin-bottom: 10px;
}
.default-container .billing-admin .billing-upgrade {
  font-size: 1.2em;
  margin: 0 4px 15px 4px;
  position: relative;
  min-height: 50px;
}
.billing-admin .coupon-input {
  height: 34px;
  border-radius: 4px;
  border: 1px solid #e5e5e5;
  margin: 0 4px 0 0;
  padding: 3px 8px;
  font-size: 1.3rem;
}
.billing-admin .coupon-input::placeholder {
  color: #D3D3D3;
}
.billing-admin .apply-coupon {
  vertical-align: top;
}
.billing-admin .apply-coupon.disabled {
  pointer-events: none;
  /*background-color: #aaa; */
  background-color: #5cb85c;
  border-color: #4cae4c;
  opacity: 0.9;
}
.billing-admin .coupon-status {
  margin-bottom: 10px;
}

.billing-admin .coupon-status {
  margin-top: 4px;
  font-size: 1.1rem;
}
.billing-admin .coupon-status .info {
  color: #00aa00;
}
.billing-admin .coupon-status .coupon-error {
  color: #D9534F;
}

.billing-admin h4 {
  margin: 20px 10px 10px 10px;
  font-size: 1.4rem;
}
.default-container .billing-admin .default-block .subscription-row.coupon-list {
  padding-top: 0;
  padding-bottom: 0;
}
.default-container .billing-admin .default-block .subscription-row table {
  width: 100%;
}
.default-container .billing-admin .default-block .subscription-row .name-small {
  vertical-align: middle;
}
.default-container .billing-admin .default-block .subscription-row .price {
  vertical-align: middle;
  min-width: 60px;
  text-align: right;
}
.billing-admin .coupon {
  height: 40px;
  font-size: 1.4rem;
  line-height: 1.4rem;
  font-family: 'Lato', Helvetica, sans-serif;
  border-bottom: 1px solid rgba(168,168,168,0.10);
  padding: 12px 0;
}
.billing-admin .coupon:last-child {
  border-bottom: none;
}
.billing-admin .coupon .coupon-code {
  font-weight: bold;
  color: #416379;
}
.billing-admin .coupon .description {
  color: #1D0000;
}

.billing-invoice td div {
  white-space: nowrap;
}

.billing-invoice table .download {
  text-align: center;
}

.billing-invoice table .total {
  text-align: right;
}

.billing-invoice table .status {
  text-transform: capitalize;
}

.billing-invoice table .download i {
  cursor: pointer;
}

.invoice-loading.ng-animate {
  animation: none;
}

@media (max-width: 1049px) {
  .billing-invoice {
    width: 100%;
  }
}