@media screen and (min-width: 376px) and (max-width: 639px) {
  .account-playlist.image-list .station-container {
    width: 170px;
    min-width: 170px;
    margin: 0 5px 10px 5px;
  }
  .account-playlist.image-list .cell-background {
    width: 170px;
    min-width: 100px;
    height: 170px;
    min-height: 100px;
  }
  .account-playlist.image-list .cell {
    width: 170px;
    min-width: 100px;
    height: 170px;
    min-height: 100px;
  }
  .account-playlist.image-list .station-container .content-rating-icon {
    top: 140px;
  }
}

@media screen and (max-width: 375px) {
  .account-playlist.image-list {
    width: 300px;
  }

  .account-playlist.image-list .station-container {
    width: 140px;
    min-width: 140px;
    margin: 0 5px 10px 5px;
  }
  .account-playlist.image-list .station-container .content-rating-icon {
    top: 120px;
  }
  .account-playlist.image-list .station-container content-rating-icon .tooltip .tooltip-inner {
    min-width: 110px;
  }
