    height: 140px;
    min-height: 100px;
  }
  .account-playlist.image-list .cell .btn {
    margin: 0 14px;
  }
  .account-playlist.image-list .cell .btn.play-now {
    margin-top: 36px;
    margin-bottom: 6px;
  }
  .account-playlist.image-list .cell .btn.play-next {
    margin-bottom: 22px;
  }
  .account-playlist.image-list .cell .btn.play-prefetch {
    margin-bottom: 22px;
  }
  .account-playlist.image-list .station-info .title {
    font-size: 1.3rem;
    line-height: 2.25rem;
  }
  .account-playlist.image-list .now-playing .play-indicator {
