@media screen and (min-width: 426px) and (max-width: 1024px) {
  .account-playlist.image-list .cell .btn.play-now {
    margin-top: 42px;
    margin-bottom: 22px;
  }
  .account-playlist.image-list .cell .btn.play-next {
    margin-bottom: 42px;
  }
  .account-playlist.image-list .cell .btn.play-prefetch {
    margin-bottom: 42px;
  }
}

.account-playlist-container {
  padding: 30px 0px 30px 0px;
}

.account-playlists .messaging-toolbar label, .account-playlists input[type='radio'], .account-playlists td.label-for {
  cursor: pointer;
}

.account-playlists .play-control {
  width: 52px;
  min-width: 52px;
  max-width: 52px;
  padding: 0 15px 0 25px;
}

.account-playlists .play-control i {
  display: none;
  font-size: 1.75rem;
}
.account-playlists .play-control.displayed i.play-icon,
.account-playlists .play-control.mobile i.play-icon
{
  display: block;
  color: #0CACE4;
}

.account-playlists .play-control.mobile.selected i.play-icon {
  display: none;
}

.account-playlists .play-control.selected i.speaker-icon {
  display: block;
}

.account-playlists tr:hover .play-control.selected i.play-icon {
  display: none;
}

.account-playlists table {
  width: 100%;
  font-family: "Lato", sans-serif;
}

.account-playlists .name {
  width: 26%;
  min-width: 26%;
  font-weight: 400;
  font-size: 14px;
  color: #333333;
}

.account-playlists .description.stations {
  width: 85%;
}

.account-playlists .description.mobile,
.account-playlists .messages.mobile {
  width: 75%;
}
div.playlist-blurb {
  position: relative;
  height: 40px;
  padding: 12px 16px;
  background: #D9EDF7;
}

div.playlist-blurb .button-bar {
  position: absolute;
  top: 0;
  right: 10px;
}
.account-playlists .actions {
  width: 20%;
  min-width: 280px;
  text-align: right;
  padding: 5px 4px;
}
.account-playlists .actions .btn {
  display: none;
}
.account-playlists .list-view-row:hover .actions .btn,
.account-playlists .actions:hover .btn {
  display: inline-block;
}

.account-playlists .rating {
  width: 7%;
  min-width: 75px;
}
.account-playlists .rating .content-rating-icon {
  width: 20px;
  display: inline-block;
}
.account-playlists .rating .content-rating-icon .bg {
  background-color: transparent;
}

.account-playlists .created-by {
  width: 7%;
}

.account-playlists .list-view-row .actions.active .btn {
  display: inline-block;
}

.account-playlists .actions .play-now.btn,
.account-playlists .actions .play-next.btn,
.account-playlists .actions .play-prefetch.btn {
  width: auto;
}

.account-playlists .no-companystations {
  text-align: left;
  margin: -15px 0px 0px;
  height: 80px;
  padding-left: 20px;
  padding-top: 30px;
  clear: both;
  color: #999;
  font-size: 1.5em;
  font-family: "Lato", sans-serif;
  font-weight: 300;
}

.account-playlists .no-results-message {
