}

.create_companystation .content-form {
  padding-bottom: 0;
}

.station .station-cell-background {
  margin: 0 14px 30px 14px;
  background-size: cover;
  width: 225px;
  min-width: 225px;
  height: 225px;
  min-height: 225px;
  color: #fff;
  pointer-events: all;
  cursor: pointer;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.21);
  transition: all .3s cubic-bezier(.25, .8, .25, 1);
}

.station .cell {
  background: rgba(0, 0, 0, 0.5);
  transition: all .5s ease;
  min-width: 100%;
  min-height: 100%;
  pointer-events: none;
  position: relative;
  color: white;
}

.station .cell .inner-chamber {
  display: table-cell;
  width: 225px;
  min-width: 225px;
  height: 225px;
  min-height: 225px;
  vertical-align: middle;
  padding-top: 15px;
  pointer-events: none;
}

.station .station-title {
  text-align: center;
  font-size: 2.125rem;
  font-weight: 400;
  padding: 0 30px;
  pointer-events: none;
}

.station .station-tagline {
  pointer-events: none;
  font-size: 1.5rem;
  text-align: center;
  position: absolute;
  bottom: 5%;
  width: 100%;
}

@media (max-width: 860px) {
  .account-playlists.tabs .navbar-default .nav-link {
    padding: 3px 4px 8px 4px;
  }
}

@media screen and (max-width: 768px) {
  div.playlist-blurb {
    height: auto;
    padding: 12px 0 0 16px;
  }
  div.playlist-blurb .blurb {
    display: block;
  }
  div.playlist-blurb .button-bar {
    position: relative;
    top: auto;
    right: auto;
    height: 40px;
  }
}
@media screen and (max-width: 500px) {
  div.playlist-blurb i.fa {
    display: inline-block;
    vertical-align: top;
  }
  div.playlist-blurb span {
    display: inline-block;
    width: calc(100% - 25px);
    vertical-align: middle;
  }
  .account-playlists .name {
    font-size: 1.7rem;
    width: 100%;
    padding-left: 10px;
  }
  .account-playlists .actions {
    min-width: 180px;
  }
  .account-playlists .create-playlist-button.mobile {
    margin-left: 50px;
  }
  .account-playlists .play-control.mobile {
    width: 10%;
  }
  .account-playlists .list-view-row .actions .btn.edit-playlist,
  .account-playlists .list-view-row .actions .btn.delete-playlist {
    display: none;
  }
  .account-playlists .list-view-row .actions.active .btn.edit-playlist,
  .account-playlists .list-view-row .actions.active .btn.delete-playlist {
    display: none;
  }

  .account-playlists .name {
    width: 90% !important;
  }

  .account-playlists .description {
    display: none;
  }
  .account-playlists .messages {
    display: none;
  }
  .account-playlists .messaging-toolbar .messages {
    display: block;
    width: 100%;
  }
}

