.streamed-container .table-container {
  margin-top: 30px;
}
.streamed-container table tbody {
  overflow-x: visible;
  overflow-y: auto;
  max-height: calc(100vh - 290px);
  display: block;
  width: 100%;
}
.streamed-container table td {
  position: relative;
}
.streamed-container table td.stream-content-song.timestamp {
  width: 150px;
  min-width: 150px;
}
.streamed-container table td.stream-content-song.metadata {
  width: 100%;
  min-width: 150px;
}
.streamed-container table td.stream-content-stream,
.streamed-container table td.stream-content-feedback {
  width: 120px;
  min-width: 120px;
}
.streamed-container table td.stream-content-feedback.disabled button {
  opacity: 0.5;
  pointer-events: none;
}
.streamed-container .stream-content-song *,
.streamed-container .stream-content-metadata,
.streamed-container .stream-content-stream-details {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.streamed-container .stream-content-feedback-details {
  display: flex;
  justify-content: center;
  align-items: center;
}
.streamed-container .stream-content-timestamp, .streamed-container .stream-content-song-details {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.streamed-container .stream-content-song {
}
.streamed-container .stream-content-timestamp {
  width: 100px;
  margin-right: 30px;
  font-size: 12px;
}
.streamed-container .play-preview {
  border: 0;
  background: none;
  outline: 0;
  position: absolute;
  top: 0;
  left: 10px;
  bottom: 0;
}
.streamed-container .play-preview .icon {
  font-size: 18px;
  pointer-events: none;
}
.streamed-container .timestamp {
  margin: 0 0 0 30px;
}
.streamed-container .stream-content-art {
  min-width: 50px;
  min-height: 50px;
  background-size: cover;
  margin-right: 10px;
}
.streamed-container .stream-content-song-details .title,
.streamed-container .stream-content-song-details .albumartist,
.streamed-container .stream-content-song-details .station {
  margin: 0;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
}
.streamed-container .stream-content-metadata .title, .streamed-container .stream-content-streamname {
  font-size: 14px;
  font-weight: 600;
}
.streamed-container .stream-content-metadata .albumartist, .streamed-container .stream-content-metadata .station {
  font-size: 12px;
  font-weight: 300;
}
.streamed-container .stream-content-device-image {
  display: flex;
  font-size: 18px;
  margin-right: 5px;
}
.streamed-container .stream-content-streamname {
  max-width: 300px;
}
.streamed-container .remove-stream,
.streamed-container .like-stream {
  width: 35px;
  height: 35px;
  padding: 0;
  color: #fff;
  border: 0;
  border-radius: 100px;
  background-color: #bababa;
  z-index: 1;
  position: relative;
  font-size: 16px;
}
.streamed-container .remove-stream.selected,
.streamed-container .remove-stream:hover {
  background-color: #d9534f;
}
.streamed-container .like-stream.selected,
.streamed-container .like-stream:hover {
  background-color: #00C276;
}
.streamed-container .stream-content-remove-actions {
  display: flex;
}
.streamed-container .stream-content-remove-actions .removed-song {
  color: #bababa;
  font-weight: 400;
  font-size: 13px;
}
.streamed-container .stream-content-remove-actions .restore-song, .streamed-container .stream-content-remove-actions .removed-song {
  width: 80px;
}
.streamed-container .stream-content:hover .removed-song.non-global, .streamed-container .stream-content-remove-actions .restore-song  {
  display: none;
}
.streamed-container .stream-content:hover .restore-song {
  display: block;
}
.streamed-container .stream-content-like-actions {
  display: flex;
  justify-content: center;
  margin-left: 8px;
}
.streamed-container .message-regulation {
  margin-top: 30px;
  margin-left: 10px;
  margin-right: 60px;
}

@media screen and (max-width: 767px) {
  .streamed-container .default-block {
    padding-bottom: 0;
    padding-top: 20px;
  }
  .streamed-container table tbody {
    max-height: calc(100vh - 340px);
  }
}
@media screen and (max-width: 500px) {
  .streamed-container table {
    margin-bottom: 0;
  }
  .streamed-container table tbody {
    max-height: calc(100vh - 340px);
  }
  .streamed-container .message-regulation {
    margin-top: 0;
  }
}
