
.modal .device-stream-modal .modal-dialog {
  width: 100%;
  max-width: 600px;
}
@media screen and (max-width: 620px) {
  .modal .device-stream-modal .modal-dialog {
    max-width: calc(100vw - 20px);
  }
}

.modal .device-stream-modal .device-specific-info .device-details {
  padding: 3px 0 15px 0;
}
.modal .device-stream-modal .device-specific-info .device-details .device-name {
  font-size: 1.75rem;
}
.modal .device-stream-modal .device-specific-info .device-details .device-id {
  font-size: 0.9rem;
  padding-left: 1px;
}
.modal .device-stream-modal .location-field,
.modal .device-stream-modal .stream-field {
  display: inline-block;
  width: 49%;
  vertical-align: top;
}
.modal .device-stream-modal .stream-select.disabled button {
  pointer-events: none;
  cursor: default;
  opacity: 0.6;
}

.modal .device-stream-modal .stream-select input, .modal .device-stream-modal .location-select input {
  padding: 6px 12px;
  height: 34px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  width: 100%;
}
.modal .device-stream-modal .select input[disabled] {
  background: #F0F0F0;
  border: 1px solid #dbdbdb;
}
.modal .device-stream-modal .select ol, .modal .device-stream-modal .location-select input {
  width: calc(100% - 10px);
}
.modal .device-stream-modal .select ol button {
  padding: 6px 20px 6px 12px;
}
.modal .device-stream-modal .modal-body {
  display: block;
}
.modal .device-stream-modal .modal-body .alert p strong {
  font-family: "Lato-Bold", sans-serif;
}
.modal .device-stream-modal .error {
  color: #d9534f;
}
.modal .device-stream-modal ul.dropdown-menu {
  max-height: 300px;
  overflow: auto;
}
.modal .device-stream-modal ul.dropdown-menu li.active a {
  background-color: #eee;
  color: black;
}
.modal .device-stream-modal ul.dropdown-menu li.active.selected a {
  background-color: #337ab7;
  color: white;
}

.modal .device-stream-modal .warning-message {
  padding-top: 20px;
}

.modal .device-stream-modal .nya-bs-select .dropdown-toggle .filter-option {
  text-overflow: ellipsis;
}
