.device-api-key-modal .subtitle strong {
  font-weight: bold;
}
.device-api-key-modal input.apikey-input {
  height: 34px;
  width: calc(100% - 70px);
  vertical-align: bottom;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding: 10px;
  margin-right: 5px;
}
.device-api-key-modal .status {
  color: green;
}

.device-api-key-modal .modal-dialog {
  max-width: 540px;
}
