.generic-public-station-filter {
  display: block;
  width: 100%;
  margin-right: 0;
  padding: 5px 15px;
}
.generic-public-station-filter ul {
  list-style: none;
  padding: 0 5px;
  margin-bottom: 0;
}
.mobile .generic-public-station-filter .playlist-ccm-station-filter {
  display: block;
}
.generic-public-station-filter .playlist-ccm-station-filter {
  width: 100%;
}
.generic-public-station-filter .playlist-ccm-station-filter i {
  margin-left: 8px;
}
.generic-public-station-filter .filter-main-screen .heading {
  padding: 8px 0;
  width: 100%;
}
button.filter-category.btn:focus {
  outline: none;
}
.generic-public-station-filter i.right {
  margin-left: 5px;
}
.generic-public-station-filter .filter-main-screen {
  position: relative;
  background-color: transparent;
  border: 0;
}
.generic-public-station-filter .filter-main-screen .separated {
  position: absolute;
  top: 0;
  right: 5px;
  padding: 8px 0 0 0;
}
.generic-public-station-filter .filter-main-screen .filter-menu {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}
.generic-public-station-filter .filter-main-screen .filter-menu button img,
.generic-public-station-filter .filter-main-screen .filter-menu button i {
  pointer-events: none;
}
.generic-public-station-filter .filter-main-screen .filter-section {
  position: relative;
  display: inline-block;
  width: 49%;
  padding: 0;
}
.generic-public-station-filter .filter-main-screen .filter-section.secondary {
  width: 32.3%;
}
.generic-public-station-filter .filter-main-screen .filter-section .pandora i {
  display: none;
}
.generic-public-station-filter .filter-main-screen .filter-section .filter-category {
  width: 100%;
  color: #333;
  padding: 4px 0 2px 0;
  text-align: center;
  margin: 3px 0;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1.2rem;
}
.generic-public-station-filter .filter-main-screen .filter-section .filter-category.active {
  background-color: #1283C7;
  border-color: #1074B0;
  color: #FFF;
}
.generic-public-station-filter .filter-main-screen .filter-section .filter-category.active img {
  filter: invert(100%);
}
.generic-public-station-filter .filter-main-screen .sub-category {
  display: none;
}
.generic-public-station-filter .filter-main-screen .open .sub-category {
  display: block;
}
.generic-public-station-filter .filter-main-screen .open .rating .sub-category.dropdown-menu {
  position: absolute;
  left: auto;
  right: 0;
}
.generic-public-station-filter .filter-main-screen .open .rating .sub-category.dropdown-menu .menu-list .divider {
  display: none;
}
.generic-public-station-filter .filter-main-screen .filter-section.category {
  width: 24%;
}
.generic-public-station-filter .filter-main-screen .filter-section i {
  font-size: 1.2rem;
  margin: 0;
  width: 15px;
}
.generic-public-station-filter .filter-main-screen .filter-section .filter-category i.right.fa-caret-down {
  width: 8px;
}

.generic-public-station-filter .filter-section .sub-category .menu-list .menu-list-item {
  cursor: pointer;
}

@media (max-width: 1200px) {
  .generic-public-station-filter .filter-main-screen .sub-category {
    max-height: 180px;
    overflow-y: auto;
  }
  .generic-public-station-filter .filter-main-screen .sub-category .menu-list li {
    font-size: 12px;
    white-space: nowrap;
    padding: 5px 10px 5px 5px;
  }
  .generic-public-station-filter .filter-main-screen .sub-category .menu-list li.divider {
    padding: 0;
  }
  .generic-public-station-filter .filter-main-screen .sub-category .menu-list li.expand {
    display: none;
  }
  .generic-public-station-filter .playlist-ccm-station-filter .category.rating.open {
    padding-bottom: 0;
  }

}

/*
@media (min-width: 992px) {
*/
@media (min-width: 1200px) {
  .generic-public-station-filter {
    display: inline-block;
    width: 200px;
    margin-right: 0;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 5px 0 45px 15px;
  }
  .generic-public-station-filter .filter-main-screen .filter-menu {
    display: block;
    flex-flow: none;
    justify-content: auto;
  }
  .generic-public-station-filter .playlist-ccm-station-filter {
    width: 100%;
  }
  .generic-public-station-filter .playlist-ccm-station-filter i {
    margin-left: 8px;
  }
  .generic-public-station-filter i.right {
    margin-left: 5px;
  }
  .generic-public-station-filter .filter-main-screen {
    background-color: transparent;
    border: 0;
  }
  .generic-public-station-filter .filter-main-screen .heading {
    padding: 8px 0;
  }
  .generic-public-station-filter .filter-main-screen .separated {
    padding: 8px 0 0 0;
  }
  .generic-public-station-filter .filter-main-screen .filter-section {
    color: #333;
    display: block;
    padding: 0;
    margin: 0;
    width: 100%;
    border: transparent;
    border-radius: 4px;
    text-align: left;
  }
  .generic-public-station-filter .filter-main-screen .filter-section .pandora i {
    display: inline-block;
  }
  .generic-public-station-filter .filter-main-screen .filter-section.secondary {
    width: 100%;
  }
  .generic-public-station-filter .filter-main-screen .filter-section.category {
    width: 100%;
  }

  .generic-public-station-filter .filter-main-screen .filter-section.open {
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .generic-public-station-filter .filter-main-screen .filter-section i.right {
    float: right;
    padding: 5px;
  }
  .generic-public-station-filter .filter-main-screen .filter-section i {
    margin: 0 5px 0 0;
    text-align: center;
  }
  .generic-public-station-filter .filter-main-screen .filter-section .filter-category {
    background-color: #F5F5F5;
    text-align: left;
    padding: 5px 10px;
    font-size: 1.4rem;
    border: transparent;
  }
  .generic-public-station-filter .filter-main-screen .filter-section .filter-category.pandora {
    padding-bottom: 2px;
  }
  .generic-public-station-filter .filter-main-screen .filter-section .filter-category.pandora img {
    height: 13px;
    margin-top: -10px;
  }
  .generic-public-station-filter .filter-main-screen .filter-section.open .filter-category {
    padding-bottom: 6px;
  }
  
  .generic-public-station-filter .filter-main-screen .filter-section,
  .generic-public-station-filter .filter-main-screen .filter-category {
    font-weight: 700;
  }
  .generic-public-station-filter .filter-main-screen .filter-section .menu-list-item {
    font-weight: 500;
  }
  
  .generic-public-station-filter .filter-main-screen ul li.category {
    border-color: transparent;
  }
  .generic-public-station-filter .filter-main-screen ul li.category.genre {
    border-top: transparent;
  }
  .generic-public-station-filter .filter-section .sub-category {
    background-color: #FAFAFA;
  }
  .generic-public-station-filter .filter-section .sub-category .menu-list {
    padding-top: 8px;
    padding-bottom: 35px;
    position: relative;
  }

  .generic-public-station-filter .filter-section .sub-category .menu-list .menu-list-item {
    padding: 1px 6px;
  }

  .generic-public-station-filter .filter-section .sub-category .menu-list .expand {
    position: absolute;
    right: 5px;
    bottom: 5px;
  }

}
