.region-container {
  margin-bottom: 60px;
}

.regions {
  margin-top: 41px;
}

.regions-general-info {
  background-color: #FAFAFA;
  margin: 60px 0 10px;
  padding: 12px;
}

.regions-general-info p,
.regions-general-info ul li {
  font-weight: 400;
}

.region-container .header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.region-container .header .multiselect-row {
  min-width: 320px;
}

.region-container table td {
  font-weight: 300;
}

.region-container table > thead > tr > th {
  border-bottom: none;
  padding: 8px;
}

.region-container table .header-cell {
  padding-left: 2px;
}

.region-container th .fa-check-circle,
.region-container td .fa-check-circle,
.region-container th .fa-circle-thin,
.region-container td .fa-circle-thin {
  font-size: 1.8rem;
}

.region-container th .fa-check-circle,
.region-container td .fa-check-circle {
  color: #5CB85C;
}

.region-container th .fa-circle-thin,
.region-container td .fa-circle-thin {
  color: #E4E4E4;
}

.region-container table th:first-child,
.region-container table td:first-child {
  width: 20px;
}

.region-container .region-name {
  width: 35%;
}

.region-container a.disabled, .region-container .fa.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.region-container .note .region-data {
  font-weight: 600;
}

.region-container .region-name .title-edit-input {
  font-size: 1.4rem;
  font-weight: normal;
  letter-spacing: 0.5px;
  line-height: 20px;
  width: 100%;
}

.region-container .pagination-controls {
  align-items: baseline;
  display: flex;
  justify-content: space-between;
  padding-bottom: 0;
}

.region-container .pagination-controls dir-pagination-controls {
  margin-right: 10px;
  position: relative;
}

.region-container table td.row-actions {
  min-width: 100px;
}

@media (min-width: 768px) {
  .regions {
    margin-top: 0;
  }

  .region-container {
    margin-bottom: 0;
  }

  .regions-general-info {
    margin-bottom: 20px;
    margin-top: 0;
  }

  .region-container table {
    margin-top: 5px;
  }
}