.autologin-url-modal .url-label strong {
  font-weight: bold;
}
.autologin-url-modal input.url-input {
  height: 34px;
  width: calc(100% - 70px);
  vertical-align: bottom;
  border-radius: 5px;
  border: 1px solid #ddd;
  padding: 10px;
  margin-right: 5px;
}
.autologin-url-modal .status {
  color: green;
}
.modal .modal-body.autologin-url-modal .row.alert.regeneration-info {
  margin-top: 18px;
}
.modal .modal-body.autologin-url-modal .row.alert.regeneration-info p {
  margin: 0;
}
.modal .modal-body.autologin-url-modal .row.alert.regeneration-info a {
  padding: 6px 0;
}

