.refresh-cloudbox-modal .zone-info {
  display: block;
  text-align: left;
  margin: 10px 20px 0;
  max-width: 100%;
}

.refresh-cloudbox-modal .zone-info .info-item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow-x: hidden;
}

.refresh-cloudbox-modal .info-item span {
  padding-right: 10px;
  text-align: right;
  flex: 0 0 80px;
}

.refresh-cloudbox-modal .info-item strong {
  overflow: hidden;
  text-overflow: ellipsis;
  flex: 1 1 80px;
}

.refresh-cloudbox-modal .modal-footer .btn {
  width: auto;
  min-width: 80px;
}

.refresh-cloudbox-modal label {
  font-weight: normal;
  display: block;
}
